import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData2, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, SelectField, InputInGroup} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import {AlertifyError, AlertifySuccess} from "../../services/AlertifyService";

const Rest = new RestService();

const Messages = new MessageService();

class CboEstudanteList extends BasePageList
{
	static defaultProps = {
		urlBase: 'experiencia-profissional-estudante/all',
		title: 'menu.experiencia_profissional_estudante.title',
		fields: [
			{
				label: 'page.experiencia_profissional_estudante.fields.estudante_id',
				field: "estudante_nome",
				width: "30%"
			},
			{
				label: 'page.experiencia_profissional_estudante.fields.cbo_id',
				field: "cbo_nome",
				width: "30%"
            },
            {
				label: 'page.experiencia_profissional_estudante.fields.duracao',
				field: "duracao",
				width: "30%"
            }
            
            
		]
    };
    
    actionView(event, id1, id2) {
		let url = "/" + this.props.urlBase.split("/")[0] + "/view";
		this.props.history.push({
			pathname: url,
            state: {estudante_id: id1,
                    cbo_id: id2
            }
		});
	}
    actionDelete(event, id1, id2) {

		let tr = event.currentTarget.parentElement.parentElement;
        let url = "experiencia-profissional-estudante/delete/";
        Rest.delete(url + id1 + "/" + id2, {}).then(function (resposta) {
            if (resposta.status === 200) {
                if (!resposta.data.error) {
                    AlertifySuccess([{message: resposta.data.message}]);
                    tr.remove();
                } else {
                    AlertifyError([{message: resposta.data.message}]);
                }
            }
        }).catch(function (error) {
            AlertifyError([error]);
        });
        
    }

    actionEdit(event, id1, id2){

        let url = "/" + this.props.urlBase.split("/")[0] + "/edit";
		this.props.history.push({
			pathname: url,
            state: {estudante_id: id1,
                    cbo_id: id2
            }
        });
	
    }
    
    
    	render()
	{
    let filter = <Filter fields={this.props.fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter} 
        toggle="tooltip" placement="top" titletoggle="Filtrar estudante"/>;
		return (
			<TableData2 filter={filter} onClickPage={ this.handleClickPage } title='page.experiencia_profissional_estudante.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/experiencia-profissional-estudante/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class CboEstudanteAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'experiencia-profissional-estudante/add',
		title: Messages.getMessage('menu.experiencia_profissional_estudante.title')
	};

	render()
	{
		return (
			<FormPage title="page.experiencia_profissional_estudante.add.title">
				<FormRow>
                    <SelectField empty={ true } value_name="id" name="estudante_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.experiencia_profissional_estudante.fields.estudante_id' required="required" colsize="6" url="estudante/all" />
					<SelectField empty={ true } value_name="id" name="cbo_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.experiencia_profissional_estudante.fields.cbo_id' required="required" colsize="6" url="cbo/all" />
				</FormRow>
                <FormRow>
                    <InputInGroup type="text" name="duracao" errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.experiencia_profissional_estudante.fields.duracao' required="required" autofocus="autofocus"/>
                </FormRow>
				<FormRow>
					<ButtonSubmit text="layout.form.add" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class CboEstudanteEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'experiencia-profissional-estudante/edit',
        title: Messages.getMessage('menu.experiencia_profissional_estudante.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let estudante_id = this.props.location.state.estudante_id;
        let cbo_id = this.props.location.state.cbo_id;
 
        Rest.view( "experiencia-profissional-estudante/view/" + estudante_id + "/" + cbo_id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        let estudante_id = this.props.location.state.estudante_id;
        let cbo_id = this.props.location.state.cbo_id;
 
        Rest.edit(this.props.urlBase + "/" + estudante_id + "/" + cbo_id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        console.log(this.state.estudante_id)
        console.log(this.state.cbo_id)
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
                <FormPage title="page.experiencia_profissional_estudante.edit.title">
                <FormRow>
                    
					<SelectField empty={ true } value_name="id" name="estudante_id" value={this.state.estudante_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.experiencia_profissional_estudante.fields.estudante_id' required="required" colsize="6" url="estudante/all" />

					<SelectField empty={ true } value_name="id" name="cbo_id" value={this.state.cbo_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.experiencia_profissional_estudante.fields.cbo_id' required="required" colsize="6" url="cbo/all" />
				</FormRow>
                <FormRow>
                    <InputInGroup type="text" name="duracao" value = {this.state.duracao} errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.experiencia_profissional_estudante.fields.duracao' required="required" autofocus="autofocus"/>
                </FormRow>
                    <FormRow>
                        <ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
                        <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                    </FormRow>
                </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class CboEstudanteView extends BasePageForm
{
    static defaultProps = {
        urlBase: 'experiencia-profissional-estudante/view',
        title: Messages.getMessage('menu.experiencia_profissional_estudante.title')
    };


    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let estudante_id = this.state.estudante_id;
        let cbo_id = this.state.cbo_id;
        this.props.history.push({
            pathname: url,
            state: {estudante_id: estudante_id,
                    cbo_id: cbo_id
            }
        });
    }

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let estudante_id = this.props.location.state.estudante_id;
        let cbo_id = this.props.location.state.cbo_id;
        Rest.view("experiencia-profissional-estudante/view/" + estudante_id + "/" + cbo_id , this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [{label:"Estudante: ", value:this.state.estudante_nome},{label:"CBO: ", value:this.state.cbo_nome},
                      {label: "Carga horria: ", value:this.state.duracao}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"CBO do estudante " + this.state.estudante_nome + " - " + this.state.cbo_nome} url={"#experiencia-profissional-estudante/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { CboEstudanteList, CboEstudanteAdd, CboEstudanteEdit, CboEstudanteView };
