import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
const Rest = new RestService();

const Messages = new MessageService();

class CboList extends BasePageList
{
	static defaultProps = {
		urlBase: 'cbo/all',
		title: 'menu.cbo.title',
		fields: [
			{
				label: "page.cbo.fields.id",
				field: "id",
				width: "5%"
			},
			{
				label: 'page.cbo.fields.nome',
				field: "nome",
				width: "65%"
			},
			{
				label: 'page.cbo.fields.codigo',
				field: "codigo",
				width: "30%"
			}
		]
	};

	render()
	{
    let input_fields = [{label: 'page.cbo.fields.nome', field: "nome"}];
    let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.cbo.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/cbo/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class CboAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'cbo/add',
		title: Messages.getMessage('menu.cbo.title')
	};

	render()
	{
		return (
			<FormPage title="page.cbo.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.cbo.fields.nome' required="required" colsize="6" />
				</FormRow>
                <FormRow>
					<InputInGroup type="text" name="codigo" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.cbo.fields.codigo' required="required" colsize="6" />
				</FormRow>
				<FormRow>
					<ButtonSubmit text="layout.form.add" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class CboEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'cbo/edit',
        title: Messages.getMessage('menu.cbo.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "cbo/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
            <FormPage title="page.cbo.edit.title">
                <FormRow>
                    <InputInGroup type="text" name="id" errors={ this.state.fieldErrors } enabled="False" onChange={ this.handleChange }
                                  label='page.cbo.fields.id' disabled={true} required="required" colsize="2" value={this.state.id}  />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.cbo.fields.nome' required="required" colsize="6"  value={this.state.nome}/>
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="codigo" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.cbo.fields.codigo' required="required" colsize="6"  value={this.state.codigo}/>
                </FormRow>
                <FormRow>
                    <ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
                    <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                </FormRow>
            </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class CboView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'cbo/view',
        title: Messages.getMessage('menu.cbo.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "cbo/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [{label:"Id: ", value:this.state.id},{label:"Experincia profissional: ", value:this.state.nome}, {label: "Cdigo: ", value:this.state.codigo}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Experincia profissional " + this.state.nome} url={"#cbo/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { CboList, CboAdd, CboEdit, CboView };
