import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import {Days} from "../../components/utils/Utils";
const Rest = new RestService();

const Messages = new MessageService();

class RegrasEmpresaList extends BasePageList
{
	static defaultProps = {
		urlBase: 'regras-empresa/all',
		title: 'menu.regras_empresa.title',
		fields: [
			{
				label: 'page.regras_empresa.fields.contrato_estudante_id',
				field: "contrato_estudante_id",
				width: "40%"
            },
            {
				label: 'page.regras_empresa.fields.dia_semana',
				field: "dia_semana",
				width: "40%"
			}
	
		]
	};

	render()
	{
    let input_fields = [{label: 'page.regras_empresa.fields.contrato_estudante_id', field: "contrato_estudante_id"}];
    let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.regras_empresa.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/regras-empresa/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class RegrasEmpresaAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'regras-empresa/add',
		title: Messages.getMessage('menu.regras_empresa.title')
	};

	render()
	{
        
		return (
			<FormPage title="page.regras_empresa.add.title">
				<FormRow>
					<SelectField empty={ true } value_name="id" name="dia_semana" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.regras_empresa.fields.dia_semana' required="required" colsize="6" options={Days} />
					<SelectField empty={ true } value_name="id" name="contrato_estudante_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.regras_empresa.fields.contrato_estudante_id' required="required" colsize="6" url="contrato-estudante/all" />

				</FormRow>
				<FormRow>
                    <InputInGroup type="text" class="date" name="data_inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.regras_empresa.fields.data_inicio' required="required" colsize="6" />
                    <InputInGroup type="text" class="date" name="data_fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.regras_empresa.fields.data_fim' required="required" colsize="6" />

				</FormRow>
				<FormRow>
                    <InputInGroup type="text" class="time" name="hr_inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.regras_empresa.fields.hr_inicio' required="required" colsize="6" />
                    <InputInGroup type="text" class="time" name="hr_fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.regras_empresa.fields.hr_fim' required="required" colsize="6" />

				</FormRow>

				<FormRow>
					<ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class RegrasEmpresaEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'regras-empresa/edit',
        title: Messages.getMessage('menu.regras_empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "regras-empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
                <FormPage title="page.regras_empresa.edit.title">
                    <FormRow>
                        <SelectField empty={ true } value_name="id" name="dia_semana" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.regras_empresa.fields.dia_semana' required="required" colsize="6" options={Days} value={this.state.dia_semana}/>
                        <SelectField empty={ true } value_name="id" name="contrato_estudante_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.regras_empresa.fields.contrato_estudante_id' required="required" colsize="6" url="contrato-estudante/all" value={this.state.contrato_estudante_id}/>

                    </FormRow>
                    <FormRow>
                        <InputInGroup type="text" class="date" name="data_inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.regras_empresa.fields.data_inicio' required="required" colsize="6" value={this.state.data_inicio}/>
                        <InputInGroup type="text" class="date" name="data_fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.regras_empresa.fields.data_fim' required="required" colsize="6" value={this.state.data_fim}/>

                    </FormRow>
                    <FormRow>
                        <InputInGroup type="text" class="time" name="hr_inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.regras_empresa.fields.hr_inicio' required="required" colsize="6" value={this.state.hr_inicio}/>
                        <InputInGroup type="text" class="time" name="hr_fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.regras_empresa.fields.hr_fim' required="required" colsize="6" value={this.state.hr_fim}/>

                    </FormRow>

                    <FormRow>
                        <ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
                        <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                    </FormRow>
                </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class RegrasEmpresaView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'regras-empresa/view',
        title: Messages.getMessage('menu.regras_empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "regras-empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = 
        [{label:"Id: ", value:this.state.id},
        {label:"Contrato estudante: ", value:this.state.contrato_estudante_id},
        {label:"Dia da semana: ", value:this.state.dia_semana},
        {label:"Data de incio: ", value:this.state.data_inicio},
        {label:"Data de trmino: ", value:this.state.data_fim},
        {label:"Hora de incio: ", value:this.state.hr_inicio},
        {label:"Hora de trmino: ", value:this.state.hr_fim}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Regras da empresa"} url={"#regras-empresa/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { RegrasEmpresaList, RegrasEmpresaAdd, RegrasEmpresaEdit, RegrasEmpresaView };
