import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import {Days} from "../../components/utils/Utils";
const Rest = new RestService();

const Messages = new MessageService();

class CursoFormacaoList extends BasePageList
{
	static defaultProps = {
		urlBase: 'curso-formacao/all',
		title: 'menu.curso_formacao.title',
		fields: [
			{
				label: 'page.curso_formacao.fields.nome',
				field: "nome",
				width: "65%"
			}
		
		]
	};

	render()
	{
    let input_fields = [{label: 'page.curso_formacao.fields.nome', field: "nome"}];
    let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.curso_formacao.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/curso-formacao/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class CursoFormacaoAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'curso-formacao/add',
		title: Messages.getMessage('menu.curso_formacao.title')
	};

	render()
	{
        
		return (
			<FormPage title="page.curso_formacao.add.title">

				<FormRow>
                    <InputInGroup type="text"  name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.curso_formacao.fields.nome' required="required" colsize="6" />
				</FormRow>
		

				<FormRow>
					<ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class CursoFormacaoEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'curso-formacao/edit',
        title: Messages.getMessage('menu.curso_formacao.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "curso-formacao/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
                <FormPage title="page.curso_formacao.edit.title">
                    <FormRow>
                        <InputInGroup type="text" name="nome" value={this.state.nome} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.curso_formacao.fields.nome' required="required" colsize="6" value={this.state.nome}/>
                    </FormRow>
              
                    <FormRow>
                        <ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
                        <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                    </FormRow>
                </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class CursoFormacaoView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'curso-formacao/view',
        title: Messages.getMessage('menu.curso_formacao.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "curso-formacao/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [{label:"Id: ", value:this.state.id},{label:"Nome: ", value:this.state.nome}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Curso formao " + this.state.nome} url={"#curso-formacao/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { CursoFormacaoList, CursoFormacaoAdd, CursoFormacaoEdit, CursoFormacaoView };
