import React, { Component } from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField, SelectAux, DynamicFields, LoginArea, Profile, Config, NavFlex} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import { AlertifySuccess, AlertifyError } from '../../services/AlertifyService';
import axios from 'axios';

const Rest = new RestService();

const Messages = new MessageService();

class EmpresaList extends BasePageList
{
    constructor(props) {
        super(props);

        this.actionContatos = this.actionContatos.bind(this);
    }
	static defaultProps = {
		urlBase: 'empresa/all',
		title: 'menu.empresa.title',
		fields: [
			{
				label: "page.empresa.fields.nome_fantasia",
				field: "nome_fantasia",
				width: "25%"
			},
			{
				label: "page.empresa.fields.razao_social",
				field: "razao_social",
				width: "25%"
			},
			{
				label: "page.empresa.fields.fone",
				field: "fone",
				width: "25%"
			},
			{
				label: "page.empresa.fields.cnpj",
				field: "cnpj",
				width: "25%"
			},
			{
				label: "page.empresa.fields.cidade_nome",
				field: "cidade_nome",
				width: "25%"
			}
		]
    };
    

    componentDidMount() {
        super.componentDidMount();
        if(this.props.role === '3'){
            this.setState(({
				actions: [
					{
						label: 'action.view',
						field: "view",
						handle: this.actionView
					},
				]
			}));
        }else{
            this.setState(({
                actions: [
                    {
                        label: 'action.edit',
                        field: "edit",
                        handle: this.actionEdit
                    },
                    {
                        label: 'action.delete',
                        field: "delete",
                        handle: this.actionDelete,
                        modal: 1
                    },
                    {
                        label: 'action.view',
                        field: "view",
                        handle: this.actionView
                    },
                    {
                        label: 'action.info',
                        field: "info",
                        handle: this.actionContatos
                    }
                ]
            }));
    
        }
    }


    actionContatos(event, id){
        let url = "/contato-empresa/list";

        this.props.history.push({
            pathname: url,
            state: {empresa_id : id}
        });
    }

	render()
    {
        let roleAcess = false;
		if(this.props.role === "1" || this.props.role === "2"){
			roleAcess = true;
		}else{
			roleAcess = false;
		}

        let input_fields = [{label: 'page.empresa.fields.nome_fantasia', field: "nome_fantasia"}, {label: 'page.empresa.fields.razao_social', field: "razao_social"},{label: 'page.empresa.fields.cnpj', field: "cnpj"}];
        let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}
                toggle="tooltip" placement="top" titletoggle="Filtrar empresa"/>;
        return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.empresa.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/empresa/add' onEdit={ this.handleOnEditAction }  buttonRemove={!roleAcess}
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class EmpresaAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'empresa/add',
		title: Messages.getMessage('menu.empresa.title')
    };
    constructor(props){
        super(props);
        this.handleCepChange = this.handleCepChange.bind(this);
        this.handleCepResponse = this.handleCepResponse.bind(this);
        this.handleCepCityResponse = this.handleCepCityResponse.bind(this);
    }

/*----------------------------------------------------------------------------------------------------*/
        
    handleCepChange(e){
        this.setState({
            cep : e.target.value
        })
        if(e.target.value.length === 9){
            let cep = e.target.value.replace('-', '');
            let url = "https://viacep.com.br/ws/"+cep+"/json/";
            axios({
                    method: 'GET',
                    params: '',
                    url: url,
                    headers: {
                    }
                }).then(this.handleCepResponse);
        }    
    }
    handleCepResponse(data){
        if(data.data !== undefined && !data.data.erro ){
            this.setState({
                bairro: data.data.bairro,
                endereco : data.data.logradouro +" " +  data.data.complemento,

            });
            Rest.get("cidade/all?nome_equal="+data.data.localidade,{}).then(this.handleCepCityResponse)
        }        
    }
    handleCepCityResponse(data){
        if(data.data.itens !== undefined){
            this.setState({
                uf_id:data.data.itens[0].uf_id,
                cidade_id:data.data.itens[0].id
            })
        }
    }


/*----------------------------------------------------------------------------------------------------*/

	render()
	{
        let inputs= [];
        inputs = [
            {fieldType: "input", type : "text", class : "", name : "nome", label : "Nome", maxLength: "255"},
            {fieldType: "input", type : "text", class : "", name : "cargo", label : "Cargo", maxLength: "255"},
            {fieldType: "input", type : "text", class : "", name : "email", label : "Email", maxLength: "255"},
            {fieldType: "input", type : "text", class : "phone_with_ddd", name : "telefone", label : "Telefone", maxLength: "25"},
            {fieldType: "input", type : "text", class : "sp_celphones", name : "celular", label : "Celular", maxLength: "25"}
        ]



		return (
			<FormPage title="page.empresa.add.title">
                <FormRow>
                    <InputInGroup type="text" name="razao_social" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.razao_social' required="required" colsize="15" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="nome_fantasia" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.nome_fantasia' required="required" colsize="7" />
                    <InputInGroup type="text" class="cnpj" name="cnpj" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.cnpj' required="required" colsize="5" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="cep" name="cep" errors={ this.state.fieldErrors }  onChange={ this.handleCepChange }
                                  label='page.empresa.fields.cep' required="required" colsize="2" />
                    <InputInGroup type="text" name="endereco" value={this.state.endereco} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.endereco' required="required" colsize="8" />
                    <InputInGroup type="text" name="complemento" errors={ this.state.fieldErrors }  required={false} onChange={ this.handleChange }
                                  label='page.empresa.fields.complemento' colsize="2" />

                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="bairro" value={this.state.bairro} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.bairro' required="required" colsize="3" />

                 
                     <SelectField  empty={true} nome="id" value_name="id" name="uf_id" value={this.state.uf_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                     label='page.empresa.fields.state' required="required" colsize="3" url="uf/all?rows_per_page=50"  />
                     <SelectField empty={ true }  value_name="id" name="cidade_id" value={this.state.cidade_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
		                             label='page.empresa.fields.cidade_id' required="required" colsize="3" url="cidade/all?rows_per_page=2000"  urlParameters={{uf_id : this.state.uf_id ? this.state.uf_id : 0}} />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="phone" name="fone" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.fone' required="required" colsize="5" />
                </FormRow>
                <FormRow>
                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={inputs}  label='Contatos'
                    name="contatos" />

                </FormRow>
           
				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

