class EmpresaEdit extends BasePageForm
{

    constructor(props) {
        super(props);
        this.handleCepChange = this.handleCepChange.bind(this);
        this.handleCepResponse = this.handleCepResponse.bind(this);
        this.handleCepCityResponse = this.handleCepCityResponse.bind(this);
        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'empresa/edit',
        title: Messages.getMessage('menu.empresa.title')
    };

    /*----------------------------------------------------------------------------------------------------*/
        
    handleCepChange(e){
        this.setState({
            cep : e.target.value
        })
        if(e.target.value.length === 9){
            let cep = e.target.value.replace('-', '');
            let url = "https://viacep.com.br/ws/"+cep+"/json/";
            axios({
                    method: 'GET',
                    params: '',
                    url: url,
                    headers: {
                    }
                }).then(this.handleCepResponse);
        }    
    }
    handleCepResponse(data){
        if(data.data !== undefined && !data.data.erro ){
            this.setState({
                bairro: data.data.bairro,
                endereco : data.data.logradouro +" " +  data.data.complemento,

            });
            Rest.get("cidade/all?nome_equal="+data.data.localidade,{}).then(this.handleCepCityResponse)
        }        
    }
    handleCepCityResponse(data){
        if(data.data.itens !== undefined){
            this.setState({
                uf_id:data.data.itens[0].uf_id,
                cidade_id:data.data.itens[0].id
            })
        }
    }


/*----------------------------------------------------------------------------------------------------*/
    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        let inputs= [];
        inputs = [
            {fieldType: "input", type : "text", class : "", name : "nome", label : "Nome", maxLength: "255"},
            {fieldType: "input", type : "text", class : "", name : "cargo", label : "Cargo", maxLength: "255"},
            {fieldType: "input", type : "text", class : "", name : "email", label : "Email", maxLength: "255"},
            {fieldType: "input", type : "text", class : "phone_with_ddd", name : "telefone", label : "Telefone", maxLength: "25"},
            {fieldType: "input", type : "text", class : "sp_celphones", name : "celular", label : "Celular", maxLength: "25"}
        ]


        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
			<FormPage title="page.empresa.edit.title">
                <FormRow>
                    <InputInGroup type="text" name="razao_social" value = {this.state.razao_social} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.razao_social' required="required" colsize="15" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="nome_fantasia" value = {this.state.nome_fantasia} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.nome_fantasia' required="required" colsize="7" />
                    <InputInGroup type="text" name="cnpj" class="cnpj" value = {this.state.cnpj} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.cnpj' required="required" colsize="5" />
                </FormRow>
                <FormRow>
                <InputInGroup type="text" class="cep" name="cep" value = {this.state.cep} errors={ this.state.fieldErrors }  onChange={ this.handleCepChange }
                                  label='page.empresa.fields.cep' required="required" colsize="2" />
                    <InputInGroup type="text" name="endereco" value = {this.state.endereco} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.endereco' required="required" colsize="8" />
                    <InputInGroup type="text" name="complemento" value = {this.state.complemento} errors={ this.state.fieldErrors }  required={false} onChange={ this.handleChange }
                                  label='page.empresa.fields.complemento' colsize="2" />

                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="bairro" value = {this.state.bairro} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.bairro' required="required" colsize="3" />

                   
                     <SelectField  empty={true} nome="id" value_name="id" name="uf_id" value={this.state.uf_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                     label='page.empresa.fields.state' required="required" colsize="3" url="uf/all?rows_per_page=50"  />
                     <SelectField empty={ true }  value_name="id" name="cidade_id" value={this.state.cidade_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
		                             label='page.empresa.fields.cidade_id' required="required" colsize="3" url="cidade/all?rows_per_page=2000"  urlParameters={{uf_id : this.state.uf_id ? this.state.uf_id : 0}} />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="phone" name="fone" value = {this.state.fone} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.fone' required="required" colsize="5" />
                </FormRow>
                <FormRow>
                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={inputs}  label='Contatos'
                    name="contatos" url={"contato-empresa/all?rows_per_page=1000&empresa_id="+ (this.props.location.state !== undefined? this.props.location.state.item_id : '0')}/>

                </FormRow>


				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
        );
    }
}


