class EmpresaView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'empresa/view',
        title: Messages.getMessage('menu.empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let roleAcess = false;
		if(this.props.role === "1" || this.props.role === "2"){
			roleAcess = true;
		}else{
			roleAcess = false;
		}

        let fields =
        [{label:"ID: ", value:this.state.id},
        {label:"Nome Fantasia: ", value:this.state.nome_fantasia},
        {label:"Razo Social: ", value:this.state.razao_social},
        {label:"Endereo: ", value:this.state.endereco},
        this.state.complemento ?{label:"Complemento: ", value:this.state.complemento} : {label: <b>No possui complemento</b>},
        {label:"Bairro: ", value:this.state.bairro},
        {label:"CEP: ", value:this.state.cep},
        {label:"Fone: ", value:this.state.fone},
        {label:"CNPJ: ", value:this.state.cnpj},
        {label:"Cidade: ", value:this.state.cidade_nome}
        ];

        let labelFields = [
            { nome:'Nome', email:'Email', telefone:'Telefone', celular:'Celular'},
            {empresa_nome:"Empresa", centro_estagio_nome:"Centro de Estgio" , inicio:'Inicio', fim:'Fim'}
        ]
        let navflex = ''
        if(this.props.location.state !== undefined){
            let label_navflex = [{title:"Contatos" , name_id:"contatos" ,url:"contato-empresa/all?empresa_id="+this.props.location.state.item_id},
                                {title:"Contratos" , name_id:"contratos" ,url:"contrato-empresa/all?empresa_id="+this.props.location.state.item_id}];
           navflex = <NavFlex label={label_navflex} labelFields={labelFields}/>
        }

        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Empresa " + this.state.nome_fantasia} navflex={navflex} url={"#empresa/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields} buttonEditRemove={!roleAcess}/>)
        );
    }
}

