class EmpresaProfile extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'empresa/view',
        title: Messages.getMessage('menu.empresa.title')
    };

    componentDidMount() {
        Rest.view( "empresa/view/info",this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()

    {
        let fields =
        [{label:"Nome Fantasia: ", value:this.state.nome_fantasia},
        {label:"Razo Social: ", value:this.state.razao_social},
        {label:"Endereo: ", value:this.state.endereco},
        this.state.complemento ?{label:"Complemento: ", value:this.state.complemento} : {label:"Complemento: ", value: <h6>No possui complemento</h6>},
        {label:"Bairro: ", value:this.state.bairro},
        {label:"CEP: ", value:this.state.cep},
        {label:"Fone: ", value:this.state.fone},
        {label:"CNPJ: ", value:this.state.cnpj},
        {label:"Cidade: ", value:this.state.cidade_nome}
        ];

        let contatos = []
        if(this.state.contatos !== undefined){
            for (let i = 0; i < 2 ;i++){
                contatos.push(this.state.contatos[i]);
            }
        }
        return (
            <Profile fields={fields} contato={contatos} title={this.state.nome_fantasia} url={"#empresa/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} />
        );
    }
}

