class EmpresaCadastro extends Component
{
	static defaultProps = {
		urlBase: 'empresa/add/register',
		title: Messages.getMessage('menu.empresa.title')
	};

	constructor(props)
	{
		super(props);
		this.state = {
			fieldErrors: {razao_social: null, nome_fantasia: null, cnpj: null, endereco: null, complemento: null, bairro: null, cep: null,
			fone: null},
			formErrors: []
		};
		this.handleCancel = this.handleCancel.bind(this);
		this.handleReceiveResponseRest = this.handleReceiveResponseRest.bind(this);
		this.handleOnSubmit = this.handleOnSubmit.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}

	handleReceiveResponseRest(res)
    {
        if (res.data.error)
    	{
    		let arrErrors = [];
    		if (res.data.errors !== undefined) {
				let arrKeys = Object.keys(res.data.errors.fields);
				for (let i = 0; i < arrKeys.length; i++) {
					arrErrors[arrKeys[i]] = res.data.errors.fields[arrKeys[i]].message;
				}

				this.setState({
					fieldErrors: arrErrors,
				});

				AlertifyError(res.data.errors.form);
			} else {
				AlertifyError([res.data]);
			}
    	} else {
            AlertifySuccess([{message: res.data.message}]);
            this.props.history.push('/' + this.props.urlBase.split('/')[0] + '/list');
        }
    }

    async handleOnSubmit(e) {
    	Rest.post2(this.props.urlBase, this.state).then(this.handleReceiveResponseRest);
    }

    handleChange(e)
	{
		this.setState({
				[e.target.name]: e.target.value,
                fieldErrors: []
            }
		)
    }


	handleCancel(e) {
        this.props.history.push("/login");
    }

	render()
	{
		return (
			<FormPage title="page.empresa.add.title">
                <FormRow>
                    <InputInGroup type="text" name="razao_social" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.razao_social' required="required" colsize="15" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="nome_fantasia" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.nome_fantasia' required="required" colsize="7" />
                    <InputInGroup type="text" class="cnpj" name="cnpj" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.cnpj' required="required" colsize="5" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="endereco" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.endereco' required="required" colsize="10" />
                    <InputInGroup type="text" name="complemento" errors={ this.state.fieldErrors }  required={false} onChange={ this.handleChange }
                                  label='page.empresa.fields.complemento' colsize="2" />

                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="bairro" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.bairro' required="required" colsize="3" />

                    <InputInGroup type="text" class="cep" name="cep" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.cep' required="required" colsize="3" />
                    <SelectAux  empty={true} nome="id" value_name="id" name="uf_id" value={this.state.uf_id} errors={ this.state.fieldErrors }
                        onChange={ this.handleChange } label='page.empresa.fields.state' required="required" colsize="3"
                        url="uf/all/register?rows_per_page=50"  />

                    <SelectAux empty={ true }  value_name="id" name="cidade_id" value={this.state.cidade_id} errors={ this.state.fieldErrors }
                        onChange={ this.handleChange } label='page.empresa.fields.cidade_id' required="required" colsize="3"
                        url="cidade/all/register?rows_per_page=50" urlParameters={{uf_id : this.state.uf_id ? this.state.uf_id : 0}} />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="phone" name="fone" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.empresa.fields.fone' required="required" colsize="5" />
                </FormRow>

                <LoginArea title="Criar Usurio" label1="page.user.fields.username" label2="page.user.fields.password" label3="page.user.fields.email"/>

				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}
