class VagaContratoList extends BasePageList
{
    constructor(props){
        super(props);
        this.actionConvocacao = this.actionConvocacao.bind(this);
        this.handleResponse = this.handleResponse.bind(this);
        this.addVaga = this.addVaga.bind(this);
    }
	static defaultProps = {
		urlBase: 'vaga/all?contrato_empresa_id=0',
		title: 'menu.vaga.title',
		fields: [
			{
				label: "page.vaga.fields.contrato_empresa_id",
				field: "contrato_concat",
				width: "10%"
			},
			{
				label: "page.vaga.fields.quantidade_vagas",
				field: "quantidade_vagas",
				width: "2%"
			},
			{
				label: "page.vaga.fields.curso_id",
				field: "curso_nome",
				width: "5%"
			},
			{
				label: "page.vaga.fields.ano",
				field: "ano",
				width: "5%"
			},
			{
				label: "page.vaga.fields.semestre",
				field: "semestre",
				width: "5%"
			},
			{
				label: "page.vaga.fields.conhecimento_especifico",
				field: "conhecimento_especifico",
				width: "2%"
			},
			{
				label: "page.vaga.fields.valor_bolsa",
				field: "valor_bolsa",
				width: "5%"
			}
		]
	};

    componentDidMount(){
        super.componentDidMount();

        if(this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        Rest.view("contrato-empresa/view/" + this.props.location.state.contrato_empresa_id).then(this.handleResponse)
        Rest.get("vaga/all?contrato_empresa_id=" + this.props.location.state.contrato_empresa_id).then(this.handleReceiveDataPage)
        
        console.log(this.props.location.state.contrato_empresa_id)

        this.setState(({
            actions: [
                {
                    label: 'action.edit',
                    field: "edit",
                    handle: this.actionEdit
                },
                {
                    label: 'action.delete',
                    field: "delete",
                    handle: this.actionDelete
                },
                {
                    label: 'action.view',
                    field: "view",
                    handle: this.actionView
                },
                {
                    label: 'action.info',
                    field: "info",
                    handle: this.actionConvocacao
                }
            ]
        }));
    
    }

    handleResponse(data){
        console.log(data);
        this.setState({
            contrato : data.data
        });
    }

    actionConvocacao(event, id){
        let url = "/vaga/convocacao/list";

        this.props.history.push({
            pathname: url,
            state: {vaga_id : id}
        });
    }

    addVaga(){
        let url = "/vaga/add";

        this.props.history.push({
            pathname: url,
            state: {contrato_id : this.props.location.state.contrato_empresa_id}
        });
    }

	render()
	{

        let fields = [];
        let basicView = undefined;
        if ( this.state.contrato !== undefined){
            fields = [
                {label:"ID: ", value:this.state.contrato.id},
                {label:"Inicio: ", value:this.state.contrato.inicio2},
                {label:"Fim: ", value:this.state.contrato.fim2},
                {label:"Centro de estgio: ", value:this.state.contrato.centro_estagio_nome},
                {label:"Empresa: ", value:this.state.contrato.empresa_nome}];
        
            basicView = (<BasicView removeButtons={true} title={ "Data Inicial: " + this.state.contrato.inicio + " Data Final: " + this.state.contrato.fim} url={"#contrato_empresa/edit?id=" + this.state.contrato.id}
            onClickEdit={this.onClickEdit} fields={fields}/>)
                
        }
 
		return (
			<TableData onClickExtraButton={this.addVaga} extraButton={true} buttonRemove={true} onClickPage={ this.handleClickPage } title='page.vaga.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/vaga/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction } details ={basicView}/>
		);
	}

}

