class VagaAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'vaga/add',
		title: Messages.getMessage('menu.vaga.title')
    };
    

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        this.setState(({contrato_empresa_id: this.props.location.state.contrato_id}));
        console.log(this.state.contrato_empresa_id);
    }    
	render()
	{

        let input_beneficio = [];
        input_beneficio = [
            {fieldType: "select", name : "beneficio", value_name : "id" , label:"Benefcios", labelSelect: "Selecione um benefcio", url: "beneficio/all" , select_idKey:"id"},
            {fieldType: "input", type : "text", class : "money", name : "valor", label : "Valor", maxLength: "25"}
        ]

        let input_dias = [];
            input_dias = [
                {fieldType: "input", type : "text", class : "time", name : "hora_ini", label : "Hora Inicial", maxLength: "25"},
                {fieldType: "input", type : "text", class : "time", name : "hora_fim", label : "Hora Final", maxLength: "25"},
                {fieldType: "select", name : "dias", value_name : "id" , label:"Dia da Semana", labelSelect: "Selecione um dia", url: "vaga/dias/all" , select_idKey:"dia_id"}
            ]

		return (
			<FormPage title="page.vaga.add.title">
	         	<FormRow>
		            <SelectField empty={ true } name="contrato_empresa_id" value_name="id" value={this.state.contrato_empresa_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
		                label='page.vaga.fields.contrato_empresa_id' required="required" colsize="3" url="contrato-empresa/all"  />
                    <InputInGroup type="text" name="quantidade_vagas" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.quantidade_vagas' required="required" colsize="4" />
		            <SelectField empty={ true } value_name="id" name="curso_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
		                label='page.vaga.fields.curso_id' required="required" colsize="5" url="curso-estudante/all" />
		        </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="ano" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.ano' required="required" colsize="4" />
                    <InputInGroup type="text" name="semestre" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.semestre' required="required" colsize="4" />
                    <SelectGender name='sexo_preferencial' required="required" label="page.vaga.fields.sexo_preferencial" errors={this.state.fieldErrors}
                        onChange={ this.handleChange } />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="conhecimento_especifico" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.conhecimento_especifico' required="required" colsize="4" />
                    <InputInGroup type="text" name="atividades_desenvolvidas" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.atividades_desenvolvidas' required="required" colsize="4" />
                    <InputInGroup type="text" name="responsavel_estagiario" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.responsavel_estagiario' required="required" colsize="4" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="email_responsavel" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.email_responsavel' required="required" colsize="6" />
                    <InputInGroup type="text" name="endereco_estagiario" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.endereco_estagiario' required="required" colsize="6" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="valor_bolsa" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.valor_bolsa' required="required" colsize="6" />
                    <InputInGroup type="text" name="observacao" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.observacao' required="required" colsize="6" />
                </FormRow>

                <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_beneficio}
                        name="beneficios" label='Benefcios'/>

                <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_dias}
                        name="dias" label='Dias do Estgio'/>

                    <ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />


			</FormPage>
		);
	}
}

