class VagaEdit extends BasePageForm
{

    constructor(props) {
        super(props);
        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'vaga/edit',
        title: Messages.getMessage('menu.vaga.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "vaga/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

    async handleOnSubmit(e) {
        console.log(this.state);
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        let input_beneficio = [];
            input_beneficio = [
                {fieldType: "select", name : "beneficio_nome", value_name : "id" , label:"Benefcios", labelSelect: "Selecione um benefcio", url: "beneficio/all" , select_idKey:"beneficio_id"},
                {fieldType: "input", type : "text", class : "money", name : "valor", label : "Valor", maxLength: "25"}
            ]

        let input_dias = [];
            input_dias = [
                {fieldType: "input", type : "text", class : "time", name : "hora_inicio", label : "Hora Inicial", maxLength: "25"},
                {fieldType: "input", type : "text", class : "time", name : "hora_fim", label : "Hora Final", maxLength: "25"},
                {fieldType: "select", name : "nome", value_name : "id" , label:"Dia da Semana", labelSelect: "Selecione um dia", url: "vaga/dias/all" , select_idKey:"id_dia"}
            ]
        let edit_permission;
            if(this.props.role === "1" || this.props.role === "2"){
                edit_permission = true;
            }else{
                edit_permission = false;
            }
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
			<FormPage title="page.vaga.edit.title">
        	 	<FormRow>
                    {edit_permission?
                    <SelectField empty={ true } disabled={!edit_permission} name="contrato_empresa_id" value={this.state.contrato_empresa_id} value_name="id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.vaga.fields.contrato_empresa_id' required="required" colsize="3" url="contrato-empresa/all" />
                    :''}            
		            
                    <InputInGroup type="text" name="quantidade_vagas" errors={ this.state.fieldErrors } value={this.state.quantidade_vagas}  onChange={ this.handleChange }
                        label='page.vaga.fields.quantidade_vagas' required="required" colsize="4" />
		            <SelectField empty={ true } value_name="id" name="curso_id" errors={ this.state.fieldErrors } value={this.state.curso_id} onChange={ this.handleChange }
		                label='page.vaga.fields.curso_id' required="required" colsize="5" url="curso-estudante/all" />
		        </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="ano" errors={ this.state.fieldErrors } value={this.state.ano} onChange={ this.handleChange }
                        label='page.vaga.fields.ano' required="required" colsize="4" />
                    <InputInGroup type="text" name="semestre" errors={ this.state.fieldErrors } value={this.state.semestre} onChange={ this.handleChange }
                        label='page.vaga.fields.semestre' required="required" colsize="4" />
                    <SelectGender name='sexo_preferencial' required="required" label="page.vaga.fields.sexo_preferencial" errors={this.state.fieldErrors}
                        value={this.state.sexo_preferencial} onChange={ this.handleChange } />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="conhecimento_especifico" errors={ this.state.fieldErrors } value={this.state.conhecimento_especifico} onChange={ this.handleChange }
                        label='page.vaga.fields.conhecimento_especifico' required="required" colsize="4" />
                    <InputInGroup type="text" name="atividades_desenvolvidas" errors={ this.state.fieldErrors } value={this.state.atividades_desenvolvidas} onChange={ this.handleChange }
                        label='page.vaga.fields.atividades_desenvolvidas' required="required" colsize="4" />
                    <InputInGroup type="text" name="responsavel_estagiario" errors={ this.state.fieldErrors } value={this.state.responsavel_estagiario} onChange={ this.handleChange }
                        label='page.vaga.fields.responsavel_estagiario' required="required" colsize="4" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="email_responsavel" errors={ this.state.fieldErrors } value={this.state.email_responsavel} onChange={ this.handleChange }
                        label='page.vaga.fields.email_responsavel' required="required" colsize="6" />
                    <InputInGroup type="text" name="endereco_estagiario" errors={ this.state.fieldErrors } value={this.state.endereco_estagiario} onChange={ this.handleChange }
                        label='page.vaga.fields.endereco_estagiario' required="required" colsize="6" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="valor_bolsa" errors={ this.state.fieldErrors } value={this.state.valor_bolsa} onChange={ this.handleChange }
                        label='page.vaga.fields.valor_bolsa' required="required" colsize="6" />
                    <InputInGroup type="text" name="observacao" errors={ this.state.fieldErrors } value={this.state.observacao} onChange={ this.handleChange }
                        label='page.vaga.fields.observacao' required="required" colsize="6" />
                </FormRow>

                <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_beneficio}
                        name="beneficios" label='Benefcios' url={"beneficio-vaga/all?vaga_id="+(this.props.location.state?this.props.location.state.item_id : '0')}/>

                <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_dias}
                        name="dias" label='Dias do Estgio' url={"dias-vaga/all?vaga_id="+(this.props.location.state?this.props.location.state.item_id : '0')}/>

                    <ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />

			</FormPage>
        );
    }
}


