class VagaView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'vaga/view',
        title: Messages.getMessage('menu.vaga.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "vaga/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [
            {label:"Contrato da empresa: ", value:this.state.contrato_empresa_id},
            {label:"Quantidade de vagas: ", value:this.state.quantidade_vagas},
            {label:"Curso: ", value:this.state.curso_id},
            {label:"Ano: ", value:this.state.ano},
            {label:"Semestre: ", value:this.state.semestre},
            {label:"Sexo preferencial: ", value:this.state.sexo_preferencial},
            {label:"Conhecimento especfico: ", value:this.state.conhecimento_especifico},
            {label:"Atividades desenvolvidas: ", value:this.state.atividades_desenvolvidas},
            {label:"Estagirio responsvel: ", value:this.state.responsavel_estagiario},
            {label:"Email do responsvel: ", value:this.state.email_responsavel},
            {label:"Endereo do estagirio: ", value:this.state.endereco_estagiario},
            {label:"Valor da bolsa: ", value:this.state.valor_bolsa},
            {label:"Observao: ", value:this.state.observacao}];

        let labelFields = [
            {beneficio_nome:'Benefcios', valor:'Valor'},
            {nome:'Dia', hora_inicio:'Inicio', hora_fim:'Fim'}
        ]
        let navflex = ''
        if(this.props.location.state !== undefined){
            let label_navflex = [{title:"Benefcios" , name_id:"beneficio" ,url:"beneficio-vaga/all?vaga_id="+this.props.location.state.item_id},
                                {title:"Dias da Semana" , name_id:"dias" ,url:"dias-vaga/all?vaga_id="+this.props.location.state.item_id}];
           navflex = <NavFlex label={label_navflex} labelFields={labelFields}/>
        }

        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Vaga " + this.state.nome} url={"#vaga/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields} navflex={navflex}/>)
        );
    }
}

