import React from 'react';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import { FormPage, FormRow} from '../../components/template/Layout';
import {ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import RestService from "../../services/RestService";
import {Redirect} from "react-router-dom";
import {AlertifyError, AlertifySuccess} from "../../services/AlertifyService";

const Rest = new RestService();

const Messages = new MessageService();

class ContratoTaxasAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'taxas-tipo-estudante/add',
		title: Messages.getMessage('menu.taxas.title'),
    };

    componentDidMount(){
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        this.setState({
            contrato_empresa_id: this.props.location.state.contrato_empresa_id   
        })

    }

    handleCancel(e) {
        let url = "/contrato-empresa/taxas/view";
		this.props.history.push({
			pathname: url,
			state: {contrato_id: this.props.location.state.contrato_empresa_id}
        });
    }

    handleReceiveResponseRest(res)
    {
        if (res.data.error)
    	{
    		let arrErrors = [];
    		if (res.data.errors !== undefined) {
				let arrKeys = Object.keys(res.data.errors.fields);
				for (let i = 0; i < arrKeys.length; i++) {
					arrErrors[arrKeys[i]] = res.data.errors.fields[arrKeys[i]].message;
				}

				this.setState({
					fieldErrors: arrErrors,
				});

				AlertifyError(res.data.errors.form);
			} else {
				AlertifyError([res.data]);


			}

    	} else {
            AlertifySuccess([{message: res.data.message}]);
            let url = "/contrato-empresa/taxas/view";
	    	this.props.history.push({
                pathname: url,
                state: {contrato_id: this.props.location.state.contrato_empresa_id}
            });
        }
        
    }

 
	render()
	{
		return (
			<FormPage title="page.taxas.add.title">
				<FormRow>
					<InputInGroup type="text" name="valor"  placeholder="Valor" class="money" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.taxas.fields.valor' required="required" colsize="6" />
				</FormRow>
                <FormRow>
                    <SelectField empty={ true } name="tipo_estudante_id" value_name="id"  errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.taxas.fields.tipo_estudante' required="required" url="tipo-estudante/all" colsize="6"/>
     
                </FormRow>
               
				<FormRow>
					<ButtonSubmit text="layout.form.add" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class ContratoTaxasEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.handleReceiveResponseRest = this.handleReceiveResponseRest.bind(this);
    }
    static defaultProps = {
        urlBase: 'taxas-tipo-estudante/edit',
        title: Messages.getMessage('menu.taxas.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let tipo_estudante_id = this.props.location.state.tipo_estudante_id;
        let contrato_empresa_id = this.props.location.state.contrato_empresa_id;

        Rest.view( "taxas-tipo-estudante/view/" + contrato_empresa_id + "/" + tipo_estudante_id , this.state).then(this.handleResponse);
    }

    handleReceiveResponseRest(res)
    {
        if (res.data.error)
    	{
    		console.log(res);
    		let arrErrors = [];
    		if (res.data.errors !== undefined) {
				let arrKeys = Object.keys(res.data.errors.fields);
				for (let i = 0; i < arrKeys.length; i++) {
					arrErrors[arrKeys[i]] = res.data.errors.fields[arrKeys[i]].message;
				}

				this.setState({
					fieldErrors: arrErrors,
				});

				AlertifyError(res.data.errors.form);
			} else {
				AlertifyError([res.data]);
			}
    		console.log("Log aqui:", res.data.message)
    	} else {
            AlertifySuccess([{message: res.data.message}]);
            let url = "/contrato-empresa/taxas/view"
            this.props.history.push({
                pathname: url,
                state: {contrato_id: this.props.location.state.contrato_empresa_id}
            });
        }
    }
    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        
        let tipo_estudante_id = this.props.location.state.tipo_estudante_id;
        let contrato_empresa_id = this.props.location.state.contrato_empresa_id;
        Rest.edit(this.props.urlBase + "/" + contrato_empresa_id + "/" + tipo_estudante_id, this.state).then(this.handleReceiveResponseRest)
    }
    handleCancel(e) {
        let url = "/contrato-empresa/taxas/view";
		this.props.history.push({
			pathname: url,
			state: {contrato_id: this.props.location.state.contrato_empresa_id}
        });
    }

    render()
    {
        return (
            this.state.error ?
            (< Redirect to={"/"} state={{from: this.props.location}}/>):
            <FormPage title="page.taxas.edit.title">
				<FormRow>
					<InputInGroup type="text" name="valor" errors={ this.state.fieldErrors } value={this.state.valor} onChange={ this.handleChange }
						label='page.taxas.fields.valor' required="required" colsize="6" />
				</FormRow>
                <FormRow>
                    <SelectField empty={ false } name="tipo_estudante_id" value_name="id"  value={this.state.tipo_estudante_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.taxas.fields.tipo_estudante' required="required" url="tipo-estudante/all" colsize="6"/>
     
                </FormRow>
               
				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

export { ContratoTaxasAdd, ContratoTaxasEdit}