import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
const Rest = new RestService();

const Messages = new MessageService();

class SeguradoraList extends BasePageList
{
	static defaultProps = {
		urlBase: 'seguradora/all',
		title: 'menu.seguradora.title',
		fields: [		
			{
				label: 'page.seguradora.fields.name',
				field: "nome",
				width: "40%"
            },
            {
				label: 'page.seguradora.fields.cnpj',
				field: "cnpj",
				width: "40%"
			}
		]
	};

	render()
	{
		return (
			<TableData onClickPage={ this.handleClickPage } title='page.seguradora.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/seguradora/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class SeguradoraAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'seguradora/add',
		title: Messages.getMessage('menu.seguradora.title')
	};

	render()
	{
		return (
			<FormPage title="page.seguradora.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.seguradora.fields.name' required="required" colsize="6" />
				</FormRow>
                <FormRow>
                    <InputInGroup type="text" class="cnpj" name="cpnj" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.seguradora.fields.cnpj' required="required" colsize="6" />
                </FormRow>
				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class SeguradoraEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'seguradora/edit',
        title: Messages.getMessage('menu.seguradora.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "seguradora/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
            <FormPage title="page.seguradora.edit.title">

                <FormRow>
                    <InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.seguradora.fields.name' required="required" colsize="6"  value={this.state.nome}/>
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="cnpj" name="cnpj" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.seguradora.fields.cnpj' required="required" colsize="6"  value={this.state.cnpj}/>
                </FormRow>
                <FormRow>
                    <ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
                    <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                </FormRow>
            </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class SeguradoraView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'seguradora/view',
        title: Messages.getMessage('menu.seguradora.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "seguradora/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [{label:"Id: ", value:this.state.id}, {label:"Nome: ", value:this.state.nome}, {label:"CNPJ: ", value:this.state.cnpj}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Seguradora " + this.state.nome} url={"#seguradora/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields}/>)
        );
    }
}

export {SeguradoraList, SeguradoraAdd, SeguradoraEdit, SeguradoraView };
