import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import {Days, Bool} from "../../components/utils/Utils";
const Rest = new RestService();

const Messages = new MessageService();

class ContratoEstudanteList extends BasePageList
{
	static defaultProps = {
		urlBase: 'contrato-estudante/all',
		title: 'menu.contrato_estudante.title',
		fields: [
			{
				label: 'page.contrato_estudante.fields.contrato_empresa_id',
				field: "contrato_empresa_id",
				width: "33%"
            },
            {
				label: 'page.contrato_estudante.fields.estudante_id',
				field: "estudante_id",
				width: "33%"
			},
            {
				label: 'page.contrato_estudante.fields.curso_formacao_id',
				field: "curso_formacao_id",
				width: "33%"
			}
		]
	};

	render()
	{
    let input_fields = [{label: 'page.contrato_estudante.fields.contrato_empresa_id', field: "contrato_empresa_id"}];
    let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.contrato_estudante.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/contrato-estudante/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

