class ContratoEstudanteEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'contrato-estudante/edit',
        title: Messages.getMessage('menu.contrato_estudante.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "contrato-estudante/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {

        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
                <FormPage title="page.contrato_estudante.edit.title">
                    <FormRow>
                        <SelectField empty={ true } value_name="id" name="contrato_empresa_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.contrato_empresa_id' required="required" colsize="6" url="contrato-empresa/all" value={this.state.contrato_empresa_id}/>
                        <SelectField empty={ true } value_name="id" name="estudante_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.estudante_id' required="required" colsize="6" url="estudante/all" value={this.state.estudante_id}/>
                        <SelectField empty={ true } value_name="id" name="centro_estagio_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.centro_estagio_id' required="required" colsize="6" url="centro-estagio/all" value={this.state.centro_estagio_id}/>

                    </FormRow>
                    <FormRow>
                        <SelectField empty={ true } value_name="id" name="seguradora_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.seguradora_id' required="required" colsize="6" url="seguradora/all" value={this.state.seguradora_id}/>
                        <SelectField empty={ true } value_name="id" name="curso_formacao_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.curso_formacao_id' required="required" colsize="6" url="curso-formacao/all" value={this.state.curso_formacao_id}/>
                        <SelectField empty={ true } value_name="id" name="empresa_feriado_facultativo" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.empresa_feriado_facultativo' required="required" colsize="6" options={Bool} />

                    </FormRow>
                    <FormRow>
                        <InputInGroup type="text" class="date" name="data_inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.data_inicio' required="required" colsize="6" value={this.state.data_inicio}/>
                        <InputInGroup type="text" class="date" name="data_fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.data_fim' required="required" colsize="6" value={this.state.data_fim}/>

                    </FormRow>
                    <FormRow>
                        <InputInGroup type="number" name="ch_pratica" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.ch_pratica' required="required" colsize="6" value={this.state.ch_pratica}/>
                        <InputInGroup type="number" name="ch_curso" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.ch_curso' required="required" colsize="6" value={this.state.ch_curso}/>
                        <InputInGroup type="number" name="ch_ambientacao" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.ch_ambientacao' required="required" colsize="6" value={this.state.ch_ambientacao}/>

                    </FormRow>
                    <FormRow>
                        <InputInGroup type="number" name="valor_bolsa" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.valor_bolsa' required="required" colsize="6" value={this.state.valor_bolsa}/>
                        <InputInGroup type="text" class="sp_celphones" name="numero" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.contrato_estudante.fields.numero' required="required" colsize="6" value={this.state.numero}/>

                    </FormRow>
          
                    <FormRow>
                        <ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
                        <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                    </FormRow>
                </FormPage>
        );
    }
}


