class ContratoEstudanteView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'contrato-estudante/view',
        title: Messages.getMessage('menu.contrato_estudante.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "contrato-estudante/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = 
        [{label:"Id: ", value:this.state.id},
        {label:"Contrato empresa: ", value:this.state.contrato_empresa_id},
        {label:"Estudante: ", value:this.state.estudante_id},
        {label:"Data de incio: ", value:this.state.data_inicio},
        {label:"Data de trmino: ", value:this.state.data_fim},
        {label:"Carga horria prtica: ", value:this.state.ch_pratica},
        {label:"Carga horria do curso: ", value:this.state.ch_curso},
        {label:"Carga horria de ambientao: ", value:this.state.ch_ambientacao},
        {label:"Valor da bolsa: ", value:this.state.valor_bolsa},
        {label:"Nmero: ", value:this.state.numero},
        {label:"Seguradora: ", value:this.state.seguradora_id},
        {label:"Centro de estgio: ", value:this.state.centro_estagio_id},
        {label:"Curso de formao: ", value:this.state.curso_formacao_id}, 
        {label: "Feriado facultativo: ", value:this.state.empresa_feriado_facultativo}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Contrato estudante " + this.state.id} url={"#contrato-estudante/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { ContratoEstudanteList, ContratoEstudanteAdd, ContratoEstudanteEdit, ContratoEstudanteView };
