import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import {ButtonSubmit, ButtonCancel, InputInGroup, SelectField, DynamicFields} from '../../components/template/Form';
import RestService from "../../services/RestService";
import {Redirect} from "react-router-dom";

const Rest = new RestService();

const Messages = new MessageService();

/*----------------------------------------------------------------------------------------------------*/

class ContratoEmpresaList extends BasePageList {

    constructor(props){
        super(props);
        this.actionVaga = this.actionVaga.bind(this);
    }
    static defaultProps = {
        urlBase: 'contrato-empresa/all',
        title: 'menu.contrato_empresa.title',
        fields: [
            {
                label: 'page.contrato_empresa.fields.empresa_nome',
                field: "empresa_nome",
                width: "20%"
            },
            {
                label: 'page.contrato_empresa.fields.centro_estagio_nome',
                field: "centro_estagio_nome",
                width: "20%"
            },
            {
                label: 'page.contrato_empresa.fields.inicio',
                field: "inicio",
                width: "25%"
            },
            {
                label: 'page.contrato_empresa.fields.fim',
                field: "fim",
                width: "25%"
            }
        ]
    };



    componentDidMount() {
        super.componentDidMount();
 
        this.setState(({
            actions: [
                {
                    label: 'action.edit',
                    field: "edit",
                    handle: this.actionEdit
                },
                {
                    label: 'action.delete',
                    field: "delete",
                    handle: this.actionDelete
                },
                {
                    label: 'action.view',
                    field: "view",
                    handle: this.actionView
                },
                {
                    label: 'action.info',
                    field: "info",
                    handle: this.actionVaga
                }
            ]
        }));
    
        
    }


    actionVaga(event, id){
        let url = "/contrato/vaga/list";

        this.props.history.push({
            pathname: url,
            state: {contrato_empresa_id : id}
        });
    }


	render()
	{
       let input_fields = [{label: 'page.contrato_empresa.fields.centro_estagio_nome', field: "centro_estagio_nome"}, {label: 'page.contrato_empresa.fields.inicio', field: "inicio"}, {label: 'page.contrato_empresa.fields.fim', field: "fim"}];
       let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.contrato_empresa.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/contrato-empresa/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}


}

/*----------------------------------------------------------------------------------------------------*/

class ContratoEmpresaAdd extends BasePageForm
{
    static defaultProps = {
        urlBase: 'contrato-empresa/add',
        title: Messages.getMessage('menu.contrato_empresa.title')
    };

    constructor(props){
        super(props)
        this.handleChangeButton = this.handleChangeButton.bind(this);
        this.handleReceiveOption = this.handleReceiveOption.bind(this);
        this._isMounted = false;
    }

    componentDidMount()
    {
        this._isMounted = true;
        this._isMounted && Rest.get("me").then(this.handleReceiveOption);
    }

    componentWillUnmount() {
        this._isMounted = false;

    }

    async handleReceiveOption(res)
    {
        if (res.status === 200) {
            console.log(res.data);
            this._isMounted && this.setState({
                role_id : res.data.role_id
            });
        }
    }
  
	handleChangeButton(items)
	{
		this.setState({
				taxas_estudante: items ? items : []
        });
    }



	render()
	{
    let input_tipo = [];
        input_tipo = [
            {fieldType: "select", name : "nome", value_name : "id" , label:"Tipo estudante", labelSelect: "Selecione um tipo estudante", url: "tipo-estudante/all" },
            {fieldType: "input", type : "text", class : "money", name : "valor", label : "Taxas", maxLength: "25"}
    ]


	let campo1 = []
	let campo2 = []
	if(this.state.role_id === 1 || this.state.role_id === 2){
	    campo1 = <SelectField empty={true} name="centro_estagio_id" value_name="id" errors={this.state.fieldErrors}
                 onChange={this.handleChange}
                 label='page.contrato_empresa.fields.centro_estagio_id' required="required" colsize="6" url="centro-estagio/all"/>
        campo2 = <SelectField empty={true} name="empresa_id" value_name="id" errors={this.state.fieldErrors}
                 onChange={this.handleChange}
                 label='page.contrato_empresa.fields.empresa_nome' required="required" colsize="6" url="empresa/all"/>
	}else if(this.state.role_id === 3){
	    campo1 = ""
        campo2 = <SelectField empty={true} name="empresa_id" value_name="id" errors={this.state.fieldErrors}
                  onChange={this.handleChange}
                  label='page.contrato_empresa.fields.empresa_nome' required="required" colsize="6" url="empresa/all"/>
	}else if(this.state.role_id === 4){
	    campo1 = <SelectField empty={true} name="centro_estagio_id" value_name="id" errors={this.state.fieldErrors}
                 onChange={this.handleChange}
                 label='page.contrato_empresa.fields.centro_estagio_id' required="required" colsize="6" url="centro-estagio/all"/>
        campo2 = ""
	}
   
        
        return (
			<FormPage title="page.contrato_empresa.add.title">
				<FormRow>
					<InputInGroup type="text" class="date placeholder" name="inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contrato_empresa.fields.inicio' required="required" colsize="6" />

					<InputInGroup type="text" class="date placeholder" name="fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contrato_empresa.fields.fim' required="required" colsize="6" />
				</FormRow>
                <FormRow>
                    {campo1}
                    {campo2}
				</FormRow>

                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_tipo}
                    label='Taxas p/ tipo de estudante' name="tipo_estudante" />


				<FormRow>
					<ButtonSubmit text="layout.form.add" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}


/*----------------------------------------------------------------------------------------------------*/
class ContratoEmpresaCentroAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'contrato-empresa/add',
		title: Messages.getMessage('menu.contrato_empresa.title')
	};

    constructor(props) {
        super(props);
        this.handleResponse = this.handleResponse.bind(this);
    }

    handleCancel(e){
        this.props.history.push("/centro-estagio/list");
    }
    componentDidMount() {
        let centro_estagio_id = this.props.location.search.match("centro_estagio_id=[0-9]+");
        if (centro_estagio_id === null){
           centro_estagio_id = ["=0"];
          }
        centro_estagio_id = centro_estagio_id[0].split("=")[1];
        this.setState(({centro_estagio_id: centro_estagio_id}));
        Rest.view("centro-estagio/view/" + centro_estagio_id, this.state).then(this.handleResponse);

    }



    handleResponse(data) {
           this.setState((
            data.data
        ));
    }



	render()
	{
		return (

			<FormPage title="page.contrato_empresa.add.title">
            	<FormRow>
					<InputInGroup type="text" class="date placeholder" name="inicio" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contrato_empresa.fields.inicio' required="required" colsize="6" />

					<InputInGroup type="text" class="date placeholder" name="fim" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contrato_empresa.fields.fim' required="required" colsize="6" />
				</FormRow>

                <FormRow>
                    <SelectField empty={false} name="centro_estagio_id" value_name="id" value={this.state.centro_estagio_id} disabled={true} errors={this.state.fieldErrors}
                                 onChange={this.handleChange}
                                 label='page.contrato_empresa.fields.centro_estagio_id' required="required" colsize="6" url="centro-estagio/all"/>
                    <SelectField empty={true} name="empresa_id" value_name="id" value={this.state.empresa_id} disabled={false} errors={this.state.fieldErrors}
                                 onChange={this.handleChange}
                                 label='page.contrato_empresa.fields.empresa_nome' required="required" colsize="6" url="empresa/all"/>

                </FormRow>

				<FormRow>
					<ButtonSubmit text="layout.form.add" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}


/*----------------------------------------------------------------------------------------------------*/

class ContratoEmpresaEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'contrato-empresa/edit',
        title: Messages.getMessage('menu.contrato_empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "contrato-empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

	render()
	{
        let input_tipo = [];
        input_tipo = [
            {fieldType: "select", name : "tipo_estudante_nome", value_name : "id" , label:"Tipo estudante", labelSelect: "Selecione um tipo estudante",
             url: "tipo-estudante/all",select_idKey:"tipo_estudante_id"},
            {fieldType: "input", type : "text", class : "money", name : "valor", label : "Taxas", maxLength: "25"}
    ]
        let edit_permission;
        if(this.props.role === "1" || this.props.role === "2"){
            edit_permission = true;
        }else{
            edit_permission = false;
        }

		return (
            this.state.error ?
            (< Redirect to={"/"} state={{from: this.props.location}}/>):
			
            <FormPage title="page.contrato_empresa.edit.title">
				<FormRow>
					<InputInGroup type="text" class="date placeholder" name="inicio" value={this.state.inicio} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contrato_empresa.fields.inicio' required="required" colsize="6" />

					<InputInGroup type="text" class="date placeholder" name="fim" value={this.state.fim} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contrato_empresa.fields.fim' required="required" colsize="6" />

				</FormRow>

                {edit_permission ?
                <FormRow>
                    <SelectField empty={true} disabled={!edit_permission} name="centro_estagio_id" value_name="id" value={this.state.centro_estagio_id} errors={this.state.fieldErrors}
                                    onChange={this.handleChange}
                                    label='page.contrato_empresa.fields.centro_estagio_id' required="required" colsize="6" url="centro-estagio/all"/>
                    <SelectField empty={true} disabled={!edit_permission} name="empresa_id" value_name="id" value={this.state.empresa_id} disabled={false} errors={this.state.fieldErrors}
                                onChange={this.handleChange}
                                label='page.contrato_empresa.fields.empresa_nome' required="required" colsize="6" url="empresa/all"/>

                </FormRow>

                :''}
                                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_tipo}
                        label='Taxas p/ tipo de estudante' name="tipo_estudante" url={"taxas-tipo-estudante/all?contrato_empresa_id="+(this.props.location.state?this.props.location.state.item_id : '0')} />

				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

