class ContratoEmpresaView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'contrato-empresa/view',
        title: Messages.getMessage('menu.contrato_empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "contrato-empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [
{label:"ID: ", value:this.state.id},
{label:"Inicio: ", value:this.state.inicio2},
{label:"Fim: ", value:this.state.fim2},
{label:"Centro de estgio: ", value:this.state.centro_estagio_nome},
{label:"Empresa: ", value:this.state.empresa_nome}];

        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/", state: { from: this.props.location } }}/> ) :
                (<BasicView title={ "Data Inicial: " + this.state.inicio + " Data Final: " + this.state.fim} url={"#contrato_empresa/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields}/>)
        );
    }
}
