import React, { Component } from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import { TableData, FormPage, FormRow , BasicView} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField, SelectGender,
	     SelectConduction, SelectLicenseStatus, SelectAux, DynamicButtonAux,
	     LoginArea, NavFlex, DynamicFields } from '../../components/template/Form';
import { AlertifySuccess, AlertifyError } from '../../services/AlertifyService';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import axios from 'axios';

const Rest = new RestService();
const Messages = new MessageService();

class EstudanteList extends BasePageList
{
	static defaultProps = {
		urlBase: 'estudante/all',
		title: 'menu.estudante.title',
		fields: [
			{
				label: "page.estudante.fields.id",
				field: "id",
				width: "5%"
			},
			{
				label: 'page.estudante.fields.name',
				field: "nome",
				width: "50%"
			},
			{
				label: "page.estudante.fields.email",
				field: "email",
				width: "5%"
			},
			{
				label: "page.estudante.fields.class",
				field: "curso_nome",
				width: "5%"
			},
			{
				label: "page.estudante.fields.school",
				field: "escola_nome",
				width: "20%"
			},
			{
				label: "page.estudante.fields.city",
				field: "cidade_nome",
				width: "10%"
			}
		]
	};
    componentDidMount() {
        super.componentDidMount();
        if(this.props.role === '4'){
            this.setState(({
				actions: [
					{
						label: 'action.view',
						field: "view",
						handle: this.actionView
					},
				]
			}));
        }else{
            this.setState(({
                actions: [
                    {
                        label: 'action.edit',
                        field: "edit",
                        handle: this.actionEdit
                    },
                    {
                        label: 'action.delete',
                        field: "delete",
                        handle: this.actionDelete
                    },
                    {
                        label: 'action.view',
                        field: "view",
                        handle: this.actionView
                    }
                ]
            }));
    
        }
    }
	render()
	{
        let acessRole = false;
        if (this.props.role === "4" || this.props.role === "5"){
            acessRole = true;
        }else{
            acessRole = false;
        }
		return (
			<TableData onClickPage={ this.handleClickPage } title='page.estudante.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/estudante/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction } buttonRemove={acessRole}/>
		);
	}

}

