class EstudanteAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'estudante/add',
		title: Messages.getMessage('menu.estudante.title')
	};

	constructor(props){
        super(props)
        this.handleCepChange = this.handleCepChange.bind(this);
        this.handleCepResponse = this.handleCepResponse.bind(this);
        this.handleCepCityResponse = this.handleCepCityResponse.bind(this);
    }


/*----------------------------------------------------------------------------------------------------*/
    
    handleCepChange(e){
        this.setState({
            cep : e.target.value
        })
        if(e.target.value.length === 9){
            let cep = e.target.value.replace('-', '');
            let url = "https://viacep.com.br/ws/"+cep+"/json/";
            axios({
                    method: 'GET',
                    params: '',
                    url: url,
                    headers: {
                    }
                }).then(this.handleCepResponse);
        }    
    }
    handleCepResponse(data){
        if(data.data !== undefined && !data.data.erro ){
            this.setState({
                bairro: data.data.bairro,
                endereco : data.data.logradouro +" " +  data.data.complemento,

            });
            Rest.get("cidade/all?nome_equal="+data.data.localidade,{}).then(this.handleCepCityResponse)
        }        
    }
    handleCepCityResponse(data){
        if(data.data.itens !== undefined){
            this.setState({
                uf_id:data.data.itens[0].uf_id,
                cidade_id:data.data.itens[0].id
            })
        }
    }


/*----------------------------------------------------------------------------------------------------*/

	render()
	{
        let input_curso = [];
        input_curso = [
            {fieldType: "select", name : "curso_nome", value_name : "id" , label:"Cursos", labelSelect: "Selecione um curso", url: "curso-estudante/all" },
            {fieldType: "input", type : "number", class : "", name : "carga_horaria", label : "Carga horria", maxLength: "25"}
        ]

         let input_exp = [];
        input_exp = [
            {fieldType: "select", name : "cbo_nome", value_name : "id" , label:"Experincia", labelSelect: "Selecione uma experincia", url: "cbo/all" },
            {fieldType: "input", type : "number", class : "", name : "duracao", label : "Durao", maxLength: "25"}
        ]

          let input_conhecimento = [];
        input_conhecimento = [
            {fieldType: "select", name : "conhecimento_nome", value_name : "id" , label:"Conhecimento", labelSelect: "Selecione um conhecimento", url: "conhecimento/all" }
        ]
		return (
			<FormPage title="page.estudante.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.estudante.fields.name' required="required" autofocus="autofocus"/>
					<InputInGroup type="text" class="date placeholder" name="data_nascimento" errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.estudante.fields.birthdate' required="required" />
					<SelectGender name='sexo' required="required" label="page.estudante.fields.gender" errors={this.state.fieldErrors}
					    onChange={ this.handleChange } />
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" class="cpf" name="cpf" max="12" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.cpf' required="required" onBlur={true}/>
                    <InputInGroup type="text" name="rg" max="12" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.rg' required="required"/>
                    <SelectField empty={ true } value_name="id" name="estado_civil_id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.civilstatus' required="required" url="estado-civil/all" />
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" name="nome_pai" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.fathername' required="required" />
                    <InputInGroup type="text" name="nome_mae" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.mothername' required="required"/>
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" class="cep" name="cep" max="10" errors={ this.state.fieldErrors } onChange={ this.handleCepChange }
                        label='page.estudante.fields.cep' required="required" />
                    <InputInGroup type="text" name="endereco" value={this.state.endereco} errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.estudante.fields.address' required="required" colsize="8"/>
                    
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" name="bairro" value={this.state.bairro} errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.neighborhood' required="required"/>

                    <SelectField  empty={true} value_name="id" name="uf_id"  value={this.state.uf_id?this.state.uf_id : 0} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.state' required="required"  url="uf/all?rows_per_page=50" />

                    <SelectField empty={ true } value_name="id" name="cidade_id" value={this.state.cidade_id} errors={ this.state.fieldErrors }
                    required="required" label="page.estudante.fields.city" onChange={this.handleChange} url={"cidade/all?rows_per_page=2000"} urlParameters={{uf_id: this.state.uf_id?this.state.uf_id: 0 }} />
                </FormRow>
				<FormRow>
                    <InputInGroup type="email" name="email" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.email' required="required" />
                    <InputInGroup type="text" class="phone" name="telefone" maxLength="30"  errors={ this.state.fieldErrors} onChange={ this.handleChange }
                        label='page.estudante.fields.number' required="required" />
                    <InputInGroup type="text" class="cellphone" name="celular" maxLength="30"  errors={ this.state.fieldErrors} onChange={ this.handleChange }
                        label='page.estudante.fields.mobilenumber' required="required"/>
				</FormRow>
				<FormRow>
					<SelectField empty={ true } name="escola_id" value_name="id" nome="escola" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.estudante.fields.school' required="required"  url="escola/all" />
					<SelectField empty={ true } name="grau_instrucao_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.estudante.fields.degreeeducation' required="required"  url="grau-instrucao/all" />

				</FormRow>
				<FormRow>
                    <SelectLicenseStatus name="habilitacao" errors={ this.state.fieldErrors } label='page.estudante.fields.licensestatus'
                        required="required" onChange={ this.handleChange }  colsize="3"/>
                    <SelectField empty={true} name="categoria_habilitacao_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange} disabled={this.state.habilitacao === "true" || this.state.habilitacao === true?false:true}
                        label='page.estudante.fields.license' required={this.state.habilitacao === "true" || this.state.habilitacao === true ? "required" : undefined} url="categoria-habilitacao/all" colsize="3"/>
                    <SelectConduction name="conducao_propria" errors={ this.state.fieldErrors } label='page.estudante.fields.conduction'
                        required={"required"} onChange={this.handleChange}  colsize="3"/>
                    <SelectField empty={true} name="tipo_veiculo_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange } disabled={this.state.conducao_propria === "true" || this.state.conducao_propria === true?false:true}
                        label='page.estudante.fields.vehicletype' required={this.state.conducao_propria === "true" || this.state.conducao_propria === true ? "required" : undefined}  url="tipo-veiculo/all"  colsize="3"/>
                    
				</FormRow>
				<FormRow>
                    <SelectField empty={true} name="curso_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.class' required="required"  url="curso-estudante/all" />
                    <SelectField empty={true} name="periodo_curso_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.season' required="required"  url="periodo-curso/all" />
                    <InputInGroup type="text" class="year" name="ano" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.year' required="required" />
                    <SelectField empty={true} name="centro_estagio_id" value_name="id" nome="nome_fantasia" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.internship' required="required"  url="centro-estagio/all" />
				</FormRow>
				<FormRow>
                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_curso}
                        name="curso_adicional_estudante" label='Cursos Adicionais' />

                </FormRow>
                <FormRow>
                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_conhecimento}
                        name="conhecimento_estudante" label='Conhecimentos'/>

                </FormRow>
                <FormRow>
                    <DynamicFields onChange={this.handleDynamic} errors={this.state.fieldErrors} fields={input_exp}
                        name="experiencia_profissional_estudante" label='Experincias Profissionais'/>

                </FormRow>

				<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
				<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel }/>

			</FormPage>
		);

	}
}
