class EstudanteView extends BasePageForm
{
    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'estudante/view',
        title: Messages.getMessage('menu.estudante.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "estudante/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
		));
	}
	
    render()
    {
        let acessRole = false;
        if (this.props.role === "4" || this.props.role === "5"){
            acessRole = true;
        }else{
            acessRole = false;
        }
        let fields =
        [{label:"ID: ", value:this.state.id},
        {label:"Nome Completo: ", value:this.state.nome},
		{label:"Nome da Me: ", value:this.state.nome_mae},
		{label:"Nome do Pai: ", value:this.state.nome_pai},
        {label:"Endereo: ", value:this.state.endereco},
        {label:"Bairro: ", value:this.state.bairro},
        {label:"Cidade: ",value:this.state.cidade_nome},
        {label:"UF: ",value:this.state.uf_nome},
		{label:"CEP: ", value:this.state.cep},
		{label:"Fone: ", value:this.state.telefone},
		{label:"Celular: ", value:this.state.celular},
		{label:"CPF: ", value:this.state.cpf},
		{label:"RG: ", value:this.state.rg},
		{label:"Data de Nascimento: ", value:this.state.data_nascimento2},
		{label:"Escola: ", value:this.state.escola_nome},
		{label:"Perodo do Curso: ", value:this.state.periodo_curso_nome}
        ];

        let labelFields = [
            {curso_nome:'Nome', carga_horaria:'Carga Horria'},
            {conhecimento_nome:'Conhecimento', estudante_nome:'Nome'},
            {estudante_nome:'Nome', cbo_nome:'Experiencia'}
        ]
        let navflex = ''
        if(this.props.location.state !== undefined){
            let label_navflex = [{title:"Cursos" , name_id:"contatos" ,url:"curso-adicional-estudante/all?estudante_id="+this.props.location.state.item_id},
                                {title:"Conhecimento" , name_id:"conhecimento" ,url:"conhecimento-estudante/all?estudante_id="+this.props.location.state.item_id},
                                {title:"Experincia" , name_id:"exp" ,url:"experiencia-profissional-estudante/all?estudante_id="+this.props.location.state.item_id}];
           navflex = <NavFlex label={label_navflex} labelFields={labelFields}/>
        }

        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Estudante " + this.state.nome} navflex={navflex} url={"#estudante/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields} buttonEditRemove={acessRole}/>)
        );
    }
}

