class EstudanteCadastro extends Component{

    static defaultProps = {
		urlBase: 'estudante/add/register',
		title: Messages.getMessage('menu.estudante.title')
	};

    constructor(props)
	{
		super(props);
		this.state = {
			fieldErrors: {nome: null, sexo: null, cep:null, endereco: null, bairro:null, cidade_id:null, telefone: null,
			celular: null, email: null, estado_civil_id: null, data_nascimento:null, rg: null, cpf: null,
			nome_pai:null, nome_mae:null, curso_id: null, escola_id:null, periodo_curso_id:null, grau_instrucao_id:null,
			ano:null, habilitacao:null, categoria_habilitacao_id:null, conducao_propria: null, tipo_veiculo_id: null, centro_estagio_id:null},
			formErrors: []
		};
        this.handleChangeButton = this.handleChangeButton.bind(this);
        this.handleChangeButton2 = this.handleChangeButton2.bind(this);
        this.handleChangeButton3 = this.handleChangeButton3.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
        this.handleReceiveResponseRest = this.handleReceiveResponseRest.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
	}

	handleChangeButton(items)
	{
		this.setState({
				curso: items ? items : []
        })
    }
    handleChangeButton2(items)
	{
		this.setState({
				conhecimento_estudante: items ? items : []
        })
    }
    handleChangeButton3(items)
	{
		this.setState({
			    experiencia: items ? items : []
        })
    }

    handleReceiveResponseRest(res)
    {
        if (res.data.error)
    	{
    		let arrErrors = [];
    		if (res.data.errors !== undefined) {
				let arrKeys = Object.keys(res.data.errors.fields);
				for (let i = 0; i < arrKeys.length; i++) {
					arrErrors[arrKeys[i]] = res.data.errors.fields[arrKeys[i]].message;
				}

				this.setState({
					fieldErrors: arrErrors,
				});

				AlertifyError(res.data.errors.form);
			} else {
				AlertifyError([res.data]);
			}
    	} else {
            AlertifySuccess([{message: res.data.message}]);
            this.props.history.push('/' + this.props.urlBase.split('/')[0] + '/list');
        }
    }

    async handleOnSubmit(e) {
    	Rest.post2(this.props.urlBase, this.state).then(this.handleReceiveResponseRest);
    }

    handleChange(e)
	{
		this.setState({
				[e.target.name]: e.target.value,
                fieldErrors: []
            }
		)
    }

    handleCancel(e) {
        this.props.history.push("/login");
    }

    render(){
        return(
            <FormPage title="page.estudante.add.title">
                <FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.estudante.fields.name' required="required" autofocus="autofocus"/>
					<InputInGroup type="text" class="date placeholder" name="data_nascimento" errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.estudante.fields.birthdate' required="required" />
					<SelectGender name='sexo' required="required" label="page.estudante.fields.gender" errors={this.state.fieldErrors}
					    onChange={ this.handleChange } />
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" class="cpf" name="cpf" max="12" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.cpf' required="required" />
                    <InputInGroup type="text" name="rg" max="12" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.rg' required="required"/>
                    <SelectAux empty={ true } value_name="id" name="estado_civil_id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.civilstatus' required="required" url="estado-civil/all/register" />
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" name="nome_pai" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.fathername' required="required" />
                    <InputInGroup type="text" name="nome_mae" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.mothername' required="required"/>
				</FormRow>
				<FormRow>
					<InputInGroup type="text" name="endereco" errors={ this.state.fieldErrors } onChange={ this.handleChange }
						label='page.estudante.fields.address' required="required" colsize="8"/>
                    <InputInGroup type="text" class="cep" name="cep" max="10" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.cep' required="required" />
				</FormRow>
				<FormRow>
                    <InputInGroup type="text" name="bairro" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.neighborhood' required="required"/>
                    <SelectAux  empty={true} nome="id" value_name="id" name="uf_id"  errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.state' required="required"  url="uf/all/register?rows_per_page=50" />
                    <SelectAux empty={ true } nome="id" value_name="id" name="cidade_id" errors={ this.state.fieldErrors }
                    required="required" label="page.estudante.fields.city" onChange={this.handleChange} url={"cidade/all/register?rows_per_page=2000"} urlParameters={{uf_id: this.state.uf_id?this.state.uf_id: 0 }} />
                </FormRow>
				<FormRow>
                    <InputInGroup type="email" name="email" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.email' required="required" />
                    <InputInGroup type="text" class="phone" name="telefone" maxLength="30"  errors={ this.state.fieldErrors} onChange={ this.handleChange }
                        label='page.estudante.fields.number' required="required" />
                    <InputInGroup type="text" class="cellphone" name="celular" maxLength="30"  errors={ this.state.fieldErrors} onChange={ this.handleChange }
                        label='page.estudante.fields.mobilenumber' required="required"/>
				</FormRow>
				<FormRow>
					<SelectAux empty={ true } name="escola_id" value_name="id" nome="escola" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.estudante.fields.school' required="required"  url="escola/all/register" />
					<SelectAux empty={ true } name="grau_instrucao_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.estudante.fields.degreeeducation' required="required"  url="grau-instrucao/all/register" />
					<SelectLicenseStatus name="habilitacao" errors={ this.state.fieldErrors } colsize="6" label='page.estudante.fields.licensestatus' required="required" onChange={ this.handleChange } />
				</FormRow>
				<FormRow>
					<SelectAux empty={true} name="categoria_habilitacao_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.estudante.fields.license' required="required"  url="categoria-habilitacao/all/register" />
					<SelectAux empty={true} name="tipo_veiculo_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.estudante.fields.vehicletype' required="required"  url="tipo-veiculo/all/register" />
					<SelectConduction name="conducao_propria" errors={ this.state.fieldErrors } label='page.estudante.fields.conduction' required="required" onChange={this.handleChange}/>
				</FormRow>
				<FormRow>
                    <SelectAux empty={true} name="curso_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.class' required="required"  url="curso-estudante/all/register" />
                    <SelectAux empty={true} name="periodo_curso_id" value_name="id" nome="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.season' required="required"  url="periodo-curso/all/register" />
                    <InputInGroup type="text" name="ano" errors={ this.state.fieldErrors } onChange={ this.handleChange }
                        label='page.estudante.fields.year' required="required" />
                    <SelectAux empty={true} name="centro_estagio_id" value_name="id" nome="nome_fantasia" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.estudante.fields.internship' required="required"  url="centro-estagio/all/register" />
				</FormRow>

				<DynamicButtonAux onChange={this.handleChangeButton} placeholder="Carga Horria" palavra={"CH"} value_name="id" label='Cursos Adicionais' url="curso-estudante/all/register"/>
                <DynamicButtonAux onChange={this.handleChangeButton2} value_name="id" label='Conhecimentos' removeInput={true} url="conhecimento/all/register"/>
                <DynamicButtonAux onChange={this.handleChangeButton3} placeholder="Durao" palavra={"Durao"} value_name="id" label='Experiencia Profissional' url="cbo/all/register"/>

                <LoginArea title="Criar Usurio" label1="page.user.fields.username" label2="page.user.fields.password" label3="page.user.fields.email"/>

				<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
				<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel }/>
			</FormPage>
        );
    }

}

