class ConvocacaoVagaContratoList extends BasePageList
{
    constructor(props){
        super(props);
        this.addConvocacao = this.addConvocacao.bind(this);
        this.handleResponse = this.handleResponse.bind(this);
    }
	static defaultProps = {
		urlBase: 'convocacao-vaga/all?vaga_id=0',
		title: 'menu.convocacao_vaga.title',
		fields: [

			{
				label: "page.convocacao_vaga.fields.vaga_id",
				field: "vaga_concat",
				width: "5%"
			},
			{
				label: "page.convocacao_vaga.fields.contrato_empresa_id",
				field: "contrato_concat",
				width: "5%"
			},
			{
				label: "page.convocacao_vaga.fields.data_convocacao",
				field: "data_convocacao",
				width: "5%"
			},
			{
				label: "page.convocacao_vaga.fields.nome_solicitante",
				field: "nome_solicitante",
				width: "5%"
			},
			{
				label: "page.convocacao_vaga.fields.contato_solicitante",
				field: "contato_solicitante",
				width: "5%"
			},
			{
				label: "page.convocacao_vaga.fields.data_entrevista",
				field: "data_entrevista",
				width: "5%"
			}
		]
	};


    componentDidMount(){
        super.componentDidMount();

        if(this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        Rest.view("vaga/view/" + this.props.location.state.vaga_id).then(this.handleResponse)
        Rest.get("convocacao-vaga/all?vaga_id=" + this.props.location.state.vaga_id).then(this.handleReceiveDataPage)
                
    }
    handleResponse(data){
        console.log(data);
        this.setState({
            vaga : data.data
        });
    }

    addConvocacao(){
        let url = "/convocacao-vaga/add";

        this.props.history.push({
            pathname: url,
            state: {vaga_id : this.props.location.state.vaga_id}
        });
    }


    
	render()

	{
        let fields = [];
        let basicView = undefined;
        if ( this.state.vaga !== undefined){
            fields = [
                {label:"Contrato da empresa: ", value:this.state.vaga.contrato_empresa_id},
                {label:"Quantidade de vagas: ", value:this.state.vaga.quantidade_vagas},
                {label:"Curso: ", value:this.state.vaga.curso_id},
                {label:"Ano: ", value:this.state.vaga.ano},
                {label:"Semestre: ", value:this.state.vaga.semestre},
                {label:"Sexo preferencial: ", value:this.state.vaga.sexo_preferencial},
                {label:"Conhecimento especfico: ", value:this.state.vaga.conhecimento_especifico},
                {label:"Atividades desenvolvidas: ", value:this.state.vaga.atividades_desenvolvidas},
                {label:"Estagirio responsvel: ", value:this.state.vaga.responsavel_estagiario},
                {label:"Email do responsvel: ", value:this.state.vaga.email_responsavel},
                {label:"Endereo do estagirio: ", value:this.state.vaga.endereco_estagiario},
                {label:"Valor da bolsa: ", value:this.state.vaga.valor_bolsa},
                {label:"Observao: ", value:this.state.vaga.observacao}];
        
            basicView = (<BasicView removeButtons={true} title={"Convocaes para a vaga"} url={"#contrato_empresa/edit?id=" + this.state.vaga.contrato_empresa_id}
            onClickEdit={this.onClickEdit} fields={fields}/>)
                
        }
 
		return (
			<TableData onClickExtraButton={this.addConvocacao} extraButton={true} buttonRemove={true} onClickPage={ this.handleClickPage } title='page.convocacao_vaga.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/convocacao-vaga/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction } details={basicView}/>
		);
	}

}

