class ConvocacaoVagaAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'convocacao-vaga/add',
		title: Messages.getMessage('menu.convocacao_vaga.title')
    };
    constructor(props){
        super(props)
        this.handleChangeButtonAux = this.handleChangeButtonAux.bind(this);
        this.handleChangeButton = this.handleChangeButton.bind(this);
        this.handleFilter = this.handleFilter.bind(this);
        this.handleResponse = this.handleResponse.bind(this);
    }

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        this.setState(({vaga_id: this.props.location.state.vaga_id}));
        console.log(this.state.vaga_id);
    }    

    handleChangeButtonAux(items)
	{
		this.setState({
				conhecimentos: items ? items : []
        })
    }
    handleChangeButton(items)
	{
		this.setState({
				estudantes: items ? items : []
        })
    }

    handleFilter()
    {
        Rest.post('estudante/filter', this.state).then(this.handleResponse);
    }
    handleResponse(data) {
        this.setState({
            estudanteOptions : data.data.itens
        });
    }
    
	render()
	{
        const itens = (this.state.conhecimentos || []).map(data => (
             data
         ));
        console.log(itens);

		return (
			<FormPage title="page.convocacao_vaga.add.title">
	            <FormRow>
                    <SelectField empty={ true } value_name="id" name="vaga_id" value={this.state.vaga_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.vaga_id' required="required" colsize="4" url="vaga/all" />
                    <SelectField empty={ true } value_name="id" name="contrato_empresa_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.contrato_empresa_id' required="required" colsize="4" url="contrato-empresa/all" />
                    <InputInGroup type="text"  class="date placeholder" name="data_convocacao" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.data_convocacao' required="required" colsize="4" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="nome_solicitante" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.nome_solicitante' required="required" colsize="6" />
                    <InputInGroup type="text" name="contato_solicitante" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.contato_solicitante' required="required" colsize="6" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="date_time placeholder2" name="data_entrevista" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.data_entrevista' required="required" colsize="3" onBlur={true} />
                </FormRow>
                    <TableFilter value_name="estudante_id" options={this.state.estudanteOptions} removeInput onChange={this.handleChangeButton} 
                        errors={this.state.fieldErrors} label='Estudantes' url="conhecimento-estudante/all" flag={true} title1='Estudantes Filtrados' title2='Estudantes Selecionados'/>
                <FormRow>
                    
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

