class ConvocacaoVagaEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.handleChangeButtonAux = this.handleChangeButtonAux.bind(this);
        this.handleChangeButton = this.handleChangeButton.bind(this);
        this.handleFilter = this.handleFilter.bind(this);
    }
    static defaultProps = {
        urlBase: 'convocacao-vaga/edit',
        title: Messages.getMessage('menu.convocacao_vaga.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "convocacao-vaga/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        this.setState({
            estudanteOptions : data.data.itens
        });
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
        console.log(this.state)
    }
    handleChangeButtonAux(items)
	{
		this.setState({
				conhecimentos: items ? items : []
        })
    }
    handleChangeButton(items)
	{
		this.setState({
				estudantes: items ? items : []
        })
    }
    handleFilter()
    {
        Rest.post('estudante/filter', this.state).then(this.handleResponse);
    }
   

    render()
    {
        let edit_permission;
        if(this.props.role === "1" || this.props.role === "2"){
            edit_permission = true;
        }else{
            edit_permission = false;
        }
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
			<FormPage title="page.convocacao_vaga.edit.title">
                <FormRow>
                    {edit_permission?
                    <>
                    <SelectField empty={ true } disabled={!edit_permission} value_name="id" name="vaga_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.vaga_id' required="required" colsize="4" url="vaga/all" value={this.state.vaga_id} />
                    <SelectField empty={ true } disabled={!edit_permission} value_name="id" name="contrato_empresa_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.contrato_empresa_id' required="required" colsize="4" url="contrato-empresa/all" value={this.state.contrato_empresa_id}/>
                    </>
                    :''}
                    
                    <InputInGroup type="text" class="date placeholder" name="data_convocacao" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.data_convocacao' required="required" colsize="4" value={this.state.data_convocacao} />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" name="nome_solicitante" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.nome_solicitante' required="required" colsize="6" value={this.state.nome_solicitante} />
                    <InputInGroup type="text" name="contato_solicitante" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.contato_solicitante' required="required" colsize="6" value={this.state.contato_solicitante}/>
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="date_time placeholder2" name="data_entrevista" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                        label='page.convocacao_vaga.fields.data_entrevista' required="required" colsize="6" value={this.state.data_entrevista} />
                </FormRow>
                    <TableFilter value_name="estudante_id" options={this.state.estudanteOptions} removeInput onChange={this.handleChangeButton} 
                        errors={this.state.fieldErrors} label='Estudantes' url="conhecimento-estudante/all" flag={true} title1='Estudantes Filtrados' title2='Estudantes Selecionados'/>
				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
        );
    }
}


