class ConvocacaoVagaView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'convocacao-vaga/view',
        title: Messages.getMessage('menu.convocacao_vaga.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "convocacao-vaga/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [
{label:"Vaga: ", value:this.state.vaga_id},
{label:"Contrato da empresa: ", value:this.state.contrato_concat},
{label:"Data de convocao: ", value:this.state.data_convocacao},
{label:"Nome do solicitante: ", value:this.state.nome_solicitante},
{label:"Contato do solicitante: ", value:this.state.contato_solicitante},
{label:"Data da entrevista: ", value:this.state.data_entrevista}];

        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Convocao da Vaga " + this.state.nome} url={"#convocacao-vaga/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields}/>)
        );
    }
}

export {ConvocacaoVagaList, ConvocacaoVagaAdd, ConvocacaoVagaEdit, ConvocacaoVagaView, ConvocacaoVagaContratoList};
