import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
const Rest = new RestService();

const Messages = new MessageService();

class ConhecimentoList extends BasePageList
{
	static defaultProps = {
		urlBase: 'conhecimento/all',
		title: 'menu.conhecimento.title',
		fields: [
			{
				label: 'page.conhecimento.fields.nome',
				field: "nome",
				width: "65%"
			},
			{
				label: 'page.conhecimento.fields.tipo_conhecimento_id',
				field: "tipo_conhecimento_nome",
				width: "30%"
			}
		]
	};

	render()
	{
    let input_fields = [{label: 'page.conhecimento.fields.nome', field: "nome"}, {label: 'page.conhecimento.fields.tipo_conhecimento_id', field: "tipo_conhecimento_nome"}];
    let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.conhecimento.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/conhecimento/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class ConhecimentoAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'conhecimento/add',
		title: Messages.getMessage('menu.conhecimento.title')
	};

	render()
	{
		return (
			<FormPage title="page.conhecimento.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.conhecimento.fields.nome' required="required" colsize="6" />
				</FormRow>
				<FormRow>
					<SelectField empty={ true } value_name="id" name="tipo_conhecimento_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.conhecimento.fields.tipo_conhecimento_id' required="required" colsize="6" url="tipo-conhecimento/all" />
				</FormRow>
				<FormRow>
					<ButtonSubmit text="layout.form.add" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class ConhecimentoEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'conhecimento/edit',
        title: Messages.getMessage('menu.conhecimento.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "conhecimento/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
                <FormPage title="page.conhecimento.add.title">
                    <FormRow>
                        <InputInGroup type="text" name="nome" value={this.state.nome} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.conhecimento.fields.nome' required="required" colsize="6" />
                    </FormRow>
                    <FormRow>
                        <SelectField empty={ true } value_name="id" name="tipo_conhecimento_id" value = {this.state.tipo_conhecimento_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.conhecimento.fields.tipo_conhecimento_id' required="required" colsize="6" url="tipo-conhecimento/all" />
                    </FormRow>
                    <FormRow>
                        <ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
                        <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                    </FormRow>
                </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class ConhecimentoView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'conhecimento/view',
        title: Messages.getMessage('menu.conhecimento.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "conhecimento/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [{label:"Id: ", value:this.state.id},{label:"Conhecimento: ", value:this.state.nome}, {label: "Tipo de conhecimento: ", value:this.state.tipo_conhecimento_nome}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Conhecimento " + this.state.nome} url={"#conhecimento/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { ConhecimentoList, ConhecimentoAdd, ConhecimentoEdit, ConhecimentoView };
