import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField } from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
const Rest = new RestService();

const Messages = new MessageService();

class CentroEstagioList extends BasePageList
{
    constructor(props) {
        super(props);

        this.actionContrato = this.actionContrato.bind(this);
    }
	static defaultProps = {
		urlBase: 'centro-estagio/all',
		title: 'menu.centro_estagio.title',
		fields: [



			{
				label: "page.centro_estagio.fields.razao_social",
				field: "razao_social",
				width: "20%"
			},
			{
				label: "page.centro_estagio.fields.nome_fantasia",
				field: "nome_fantasia",
				width: "20%"
			},
			{
				label: "page.centro_estagio.fields.cnpj",
				field: "cnpj",
				width: "15%"
			},
			{
				label: "page.centro_estagio.fields.email",
				field: "email",
				width: "20%"
			},
			{
				label: "page.centro_estagio.fields.matriz_nome",
				field: "matriz_nome",
				width: "20%"
			}
		]
	};

    componentDidMount() {
        super.componentDidMount();
        this.setState(({
            actions: [
                {
                    label: 'action.edit',
                    field: "edit",
                    handle: this.actionEdit
                },
                {
                    label: 'action.delete',
                    field: "delete",
                    handle: this.actionDelete
                },
                {
                    label: 'action.view',
                    field: "view",
                    handle: this.actionView
                },
                {
                    label: 'action.edit',
                    field: "info",
                    handle: this.actionContrato
                }
            ]
        }));
    }


    actionContrato(event, id){
        let url = "/contrato-empresa/centro/view";

        this.props.history.push({
            pathname: url,
            state: {centro_estagio_id: id}
        });
    }

	render()
	{
        let input_fields = [{label: 'page.centro_estagio.fields.nome_fantasia', field: "nome_fantasia"}, {label: 'page.centro_estagio.fields.razao_social', field: "razao_social"},{label: 'page.centro_estagio.fields.cnpj', field: "cnpj"}];
        let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}
            toggle="tooltip" placement="top" titletoggle="Filtrar centro de estgio"/>;

		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.centro_estagio.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/centro-estagio/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class CentroEstagioAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'centro-estagio/add',
		title: Messages.getMessage('menu.centro_estagio.title')
	};

	render()
	{
		return (
			<FormPage title="page.centro_estagio.add.title">

                <FormRow>
                    <InputInGroup type="text" name="razao_social" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.razao_social' required="required" colsize="6" />
                    <InputInGroup type="text" name="nome_fantasia" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.nome_fantasia' required="required" colsize="6" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="cnpj" name="cnpj" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.cnpj' required="required" colsize="6" />
                    <InputInGroup type="text" name="email" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.email' required="required" colsize="6" />
                </FormRow>
		        <FormRow>
					<SelectField empty={ true } value_name="id" name="matriz_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.centro_estagio.fields.matriz_id'  colsize="6" url="centro-estagio/all" />
				</FormRow>
				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/



class CentroEstagioEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    
    static defaultProps = {
        urlBase: 'centro-estagio/edit',
        title: Messages.getMessage('menu.centro_estagio.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "centro-estagio/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
            <FormPage title="page.centro_estagio.edit.title">

                <FormRow>
                    <InputInGroup type="text" name="razao_social" value={this.state.razao_social} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.razao_social' required="required" colsize="6" />
                    <InputInGroup type="text" name="nome_fantasia" errors={ this.state.fieldErrors } value={this.state.nome_fantasia}  onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.nome_fantasia' required="required" colsize="6" />
                </FormRow>
                <FormRow>
                    <InputInGroup type="text" class="cnpj" name="cnpj" errors={ this.state.fieldErrors } value={this.state.cnpj} onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.cnpj' required="required" colsize="6" />
                    <InputInGroup type="text" name="email" errors={ this.state.fieldErrors }  value={this.state.email} onChange={ this.handleChange }
                                  label='page.centro_estagio.fields.email' required="required" colsize="6" />
                </FormRow>
		        <FormRow>
					<SelectField empty={ true } value_name="id" name="matriz_id" errors={ this.state.fieldErrors } value={this.state.matriz_id} onChange={ this.handleChange }
						label='page.centro_estagio.fields.matriz_id'  colsize="6" url="centro-estagio/all"  urlParameters={{centro_remove : this.state.id }} />	
                </FormRow>
                
				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class CentroEstagioView extends BasePageForm
{

    constructor(props) {
        super(props);
        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }

    static defaultProps = {
        urlBase: 'centro-estagio/view',
        title: Messages.getMessage('menu.centro_estagio.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view("centro-estagio/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }


    render()
    {
        let fields = [
{label:"Razo Social: ", value:this.state.razao_social},
{label:"Nome Fantasia: ", value:this.state.nome_fantasia},
{label:"CNPJ: ", value:this.state.cnpj},
{label:"Email: ", value:this.state.email},
this.state.matriz_nome ?{label:"Matriz: ", value:this.state.matriz_nome} : {label: <b>No possui matriz cadastrada</b>}];

        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Centro Estagio " + this.state.nome_fantasia} url={"#centro-estagio/edit?id=" + this.state.centro_estagio_id}
                            onClickEdit={this.onClickEdit} fields={fields}/>)
        );
    }
}

export {CentroEstagioList, CentroEstagioAdd, CentroEstagioEdit, CentroEstagioView };

