import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField } from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import axios from 'axios';

const Rest = new RestService();

const Messages = new MessageService();


class EscolaList extends BasePageList
{
	static defaultProps = {
		urlBase: 'escola/all',
		title: 'menu.escola.title',
		fields: [
			{
				label: 'page.escola.fields.name',
				field: "nome",
				width: "55%"
			},
			{
				label: 'page.escola.fields.cnpj',
				field: "cnpj",
				width: "20%"
			},
			{
				label: 'page.escola.fields.cidade_nome',
				field: 'cidade_nome',
				width: "20%"
			}
		]
	};


    componentDidMount() {
        super.componentDidMount();

		if(!(this.props.role === "1" || this.props.role === "2")){
			this.setState(({
				actions: [
					{
						label: 'action.view',
						field: "view",
						handle: this.actionView
					},
				]
			}));
		}
		
    }


	render()
	{
		let roleAcess = false;
		if(this.props.role === "1" || this.props.role === "2"){
			roleAcess = true;
		}else{
			roleAcess = false;
		}

       let input_fields = [{label: 'page.escola.fields.name', field: "nome"}, {label: 'page.escola.fields.cnpj', field: "cnpj"}];
       let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter} 
	   		toggle="tooltip" placement="top" titletoggle="Filtrar escola"/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.escola.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/escola/add' onEdit={ false }  buttonRemove={!roleAcess}
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class EscolaAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'escola/add',
		title: Messages.getMessage('menu.escola.title')
	};
	constructor(props){
        super(props)
        this.handleCepChange = this.handleCepChange.bind(this);
        this.handleCepResponse = this.handleCepResponse.bind(this);
        this.handleCepCityResponse = this.handleCepCityResponse.bind(this);
    }


/*----------------------------------------------------------------------------------------------------*/
    
    handleCepChange(e){
        this.setState({
            cep : e.target.value
        })
        if(e.target.value.length === 9){
            let cep = e.target.value.replace('-', '');
            let url = "https://viacep.com.br/ws/"+cep+"/json/";
            axios({
                    method: 'GET',
                    params: '',
                    url: url,
                    headers: {
                    }
                }).then(this.handleCepResponse);
        }    
    }
    handleCepResponse(data){
        if(data.data !== undefined && !data.data.erro ){
            this.setState({
                bairro: data.data.bairro,
                endereco : data.data.logradouro +" " +  data.data.complemento,

            });
            Rest.get("cidade/all?nome_equal="+data.data.localidade,{}).then(this.handleCepCityResponse)
        }        
    }
    handleCepCityResponse(data){
        if(data.data.itens !== undefined){
            this.setState({
                uf_id:data.data.itens[0].uf_id,
                cidade_id:data.data.itens[0].id
            })
        }
    }


/*----------------------------------------------------------------------------------------------------*/

 
	render()
	{
		return (
			<FormPage title="page.escola.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.name' required="required" colsize="6" />
					<InputInGroup type="text" class="cnpj" name="cnpj" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.cnpj' required="required" colsize="6" />
				</FormRow>

				<FormRow>
					<InputInGroup type="text" name="endereco" value={this.state.endereco} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.endereco' required="required" colsize="12" />
				</FormRow>
				<FormRow>
					<InputInGroup type="text" name="bairro" value={this.state.bairro} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.bairro' required="required" colsize="6" />

					<InputInGroup type="text" class="cep" name="cep" errors={ this.state.fieldErrors }  onChange={ this.handleCepChange }
						label='page.escola.fields.cep' required="required" colsize="6" />
				</FormRow>
				<FormRow>
				<SelectField  empty={true} nome="id" value_name="id" name="uf_id" value={this.state.uf_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                     label='page.empresa.fields.state' required="required" colsize="3" url="uf/all?rows_per_page=50"  />
				<SelectField empty={ true }  value_name="id" name="cidade_id" value={this.state.cidade_id} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
		                             label='page.empresa.fields.cidade_id' required="required" colsize="3" url="cidade/all?rows_per_page=2000"  urlParameters={{uf_id : this.state.uf_id ? this.state.uf_id : 0}} />
				</FormRow>
				<FormRow>
					<InputInGroup type="text" class="phone" name="telefone" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.telefone' required="required" colsize="6" />
					<InputInGroup type="text" name="email" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.email' required="required" colsize="6" />
				</FormRow>



				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class EscolaEdit extends BasePageForm
{
	constructor(props) {
		super(props);
		this.handleCepChange = this.handleCepChange.bind(this);
        this.handleCepResponse = this.handleCepResponse.bind(this);
        this.handleCepCityResponse = this.handleCepCityResponse.bind(this);
		this.handleResponse = this.handleResponse.bind(this);
	}
	static defaultProps = {
		urlBase: 'escola/edit',
		title: Messages.getMessage('menu.escola.title')
	};

/*----------------------------------------------------------------------------------------------------*/
    
    handleCepChange(e){
        this.setState({
            cep : e.target.value
        })
        if(e.target.value.length === 9){
            let cep = e.target.value.replace('-', '');
            let url = "https://viacep.com.br/ws/"+cep+"/json/";
            axios({
                    method: 'GET',
                    params: '',
                    url: url,
                    headers: {
                    }
                }).then(this.handleCepResponse);
        }    
    }
    handleCepResponse(data){
        if(data.data !== undefined && !data.data.erro ){
            this.setState({
                bairro: data.data.bairro,
                endereco : data.data.logradouro +" " +  data.data.complemento,

            });
            Rest.get("cidade/all?nome_equal="+data.data.localidade,{}).then(this.handleCepCityResponse)
        }        
    }
    handleCepCityResponse(data){
        if(data.data.itens !== undefined){
            this.setState({
                uf_id:data.data.itens[0].uf_id,
                cidade_id:data.data.itens[0].id
            })
        }
    }


/*----------------------------------------------------------------------------------------------------*/

	componentDidMount() {
		if (this.props.location.state === undefined){
			this.setState(({error:true}));
			return;
		}
		let id = this.props.location.state.item_id;
		Rest.view( "escola/view/" + id, this.state).then(this.handleResponse);
	}

	handleResponse(data) {
		this.setState((
			data.data
		));
	}

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }
	render()
	{
		return (
			this.state.error ?
				( <Redirect to={{ pathname: "/", state: { from: this.props.location } }}/> ) :
			<FormPage title="page.escola.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" value = {this.state.nome} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.name' required="required" colsize="6" />
					<InputInGroup type="text" class="cnpj" name="cnpj" value = {this.state.cnpj} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.cnpj' required="required" colsize="6" />
				</FormRow>

				<FormRow>
					<InputInGroup type="text" name="endereco" value = {this.state.endereco} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.endereco' required="required" colsize="12" />
				</FormRow>
				<FormRow>
					<InputInGroup type="text" name="bairro" value = {this.state.bairro} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.bairro' required="required" colsize="6" />

					<InputInGroup type="text" class="cep" name="cep" value = {this.state.cep} errors={ this.state.fieldErrors }  onChange={ this.handleCepChange }
						label='page.escola.fields.cep' required="required" colsize="6" />
				</FormRow>
				<FormRow>
				<SelectField nome="id" value_name="id" name="uf_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                     label='page.empresa.fields.state' required="required" colsize="3" url="uf/all?rows_per_page=50" value={this.state.uf_id} />
				<SelectField value_name="id" name="cidade_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
		                             label='page.empresa.fields.cidade_id' required="required" colsize="3" url="cidade/all?rows_per_page=2000"  urlParameters={{uf_id : this.state.uf_id ? this.state.uf_id : 0}} value={this.state.cidade_id}/>
				</FormRow>
				<FormRow>
					<InputInGroup type="text" class="phone" name="telefone" value = {this.state.telefone} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.telefone' required="required" colsize="6" />
					<InputInGroup type="text" name="email" value = {this.state.email} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.escola.fields.email' required="required" colsize="6" />
				</FormRow>



				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class EscolaView extends BasePageForm
{

	constructor(props) {
		super(props);

		this.handleResponse = this.handleResponse.bind(this);
		this.onClickEdit = this.onClickEdit.bind(this);
	}
	onClickEdit(event) {
		let url = "edit";
		let id = this.state.id;
		this.props.history.push({
			pathname: url,
			state: {item_id: id}
		});
	}
	static defaultProps = {
		urlBase: 'escola/view',
		title: Messages.getMessage('menu.escola.title')
	};

	componentDidMount() {
		if (this.props.location.state === undefined){
			this.setState(({error:true}));
			return;
		}
		let id = this.props.location.state.item_id;
		Rest.view( "escola/view/" + id, this.state).then(this.handleResponse);
	}

	handleResponse(data) {
		this.setState((
			data.data
		));
	}
	render()
	{
		let roleAcess = false;
		if(this.props.role === "1" || this.props.role === "2"){
			roleAcess = true;
		}else{
			roleAcess = false;
		}
		let fields = [{label:"Nome: ", value:this.state.nome},{label:"Cep: ", value:this.state.cep},
		{label:"Endereo: ", value:this.state.endereco},{label:"Bairro: ", value:this.state.bairro},
		{label:"Cidade: ", value:this.state.cidade_nome},{label:"Telefone: ", value:this.state.telefone},
		{label:"Email: ", value:this.state.email},{label:"CNPJ: ", value:this.state.cnpj}];
		return (
			this.state.error ?
				( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
				(<BasicView title={"Escola " + this.state.nome} url={"#escola/edit?id=" + this.state.id} fields={fields} onClickEdit={this.onClickEdit} buttonEditRemove={!roleAcess} />)
		);
	}
}
/*----------------------------------------------------------------------------------------------------*/

export { EscolaList, EscolaAdd, EscolaView, EscolaEdit} ;