import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
import {Days} from "../../components/utils/Utils";
const Rest = new RestService();

const Messages = new MessageService();

class ModuloList extends BasePageList
{
	static defaultProps = {
		urlBase: 'modulo/all',
		title: 'menu.modulo.title',
		fields: [
			{
				label: 'page.modulo.fields.nome',
				field: "nome",
				width: "33%"
			},
			{
				label: 'page.modulo.fields.ch',
				field: "ch",
				width: "33%"
			},
			{
				label: 'page.modulo.fields.curso_id',
				field: "curso_id",
				width: "33%"
			}
		]
	};

	render()
	{
    let input_fields = [{label: 'page.modulo.fields.nome', field: "nome"}];
    let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.modulo.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/modulo/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class ModuloAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'modulo/add',
		title: Messages.getMessage('menu.modulo.title')
	};

	render()
	{
        
		return (
			<FormPage title="page.modulo.add.title">
			
				<FormRow>
                    <InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.modulo.fields.nome' required="required" colsize="6" />
                    <InputInGroup type="text" name="ch" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                            label='page.modulo.fields.ch' required="required" colsize="6" />

				</FormRow>
                <FormRow>
                    <SelectField empty={ true } value_name="id" name="curso_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						    label='page.modulo.fields.curso_id' required="required" colsize="6" url="curso-formacao/all" />

				</FormRow>
				<FormRow>
					<ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class ModuloEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'modulo/edit',
        title: Messages.getMessage('menu.modulo.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "modulo/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
        console.log(this.state)
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

    render()
    {
        return (
            this.state.error ?
                (< Redirect to={"/"} state={{from: this.props.location}}/>):
                <FormPage title="page.modulo.edit.title">
                    <FormRow>
                        <InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.modulo.fields.nome' required="required" colsize="6" value={this.state.nome}/>
                        <InputInGroup type="text" name="ch" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.modulo.fields.ch' required="required" colsize="6" value={this.state.ch}/>

                    </FormRow>
                    <FormRow>
                        <SelectField empty={ true } value_name="id" name="curso_id" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
                                label='page.modulo.fields.curso_id' required="required" colsize="6" url="curso-formacao/all" value={this.state.curso_id}/>

                    </FormRow>
                    <FormRow>
                        <ButtonSubmit text="layout.form.save" onClick={ this.handleOnSubmit } />
                        <ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
                    </FormRow>
                </FormPage>
        );
    }
}


/*----------------------------------------------------------------------------------------------------*/

class ModuloView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'modulo/view',
        title: Messages.getMessage('menu.modulo.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "modulo/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        let fields = [{label:"Id: ", value:this.state.id},
        {label:"Nome: ", value:this.state.nome},
        {label: "Carga horria: ", value:this.state.ch},
        {label: "Curso: ", value:this.state.curso_id}];
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Mdulo " + this.state.nome} url={"#modulo/edit?id=" + this.state.id} fields={fields}
                            onClickEdit={this.onClickEdit}/>)
        );
    }
}

export { ModuloList, ModuloAdd, ModuloEdit, ModuloView };
