import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import {ButtonSubmit, ButtonCancel, InputInGroup, SelectField} from '../../components/template/Form';
import RestService from "../../services/RestService";
import {Redirect} from "react-router-dom";
//import {AlertifyError, AlertifySuccess} from "../../services/AlertifyService";

const Rest = new RestService();

const Messages = new MessageService();

/*----------------------------------------------------------------------------------------------------*/

class ContatoEmpresaList extends BasePageList {

    constructor(props){
        super(props);
        this.handleResponse = this.handleResponse.bind(this);
    }


    static defaultProps = {
        urlBase: 'contato-empresa/all' ,
        title: 'menu.contato_empresa.title',
        fields: [
            {
                label: 'page.contato_empresa.fields.nome',
                field: "nome",
                width: "30%"
            },
            {
                label: 'page.contato_empresa.fields.cargo',
                field: "cargo",
                width: "30%"
            },
            {
                label: 'page.contato_empresa.fields.email',
                field: "email",
                width: "30%"
            },
        ]
    };    

    componentDidMount() {
        super.componentDidMount();
        if (this.props.location.state === undefined){
            this.setState(({error:true}));

//            let url = "/empresa/list";

  //          this.props.history.push({
    //            pathname: url
      //      });;
            return;
        }else{
            let empresa_id = this.props.location.state.empresa_id;
            this.setState({empresa_id : empresa_id});
            Rest.get("empresa/view/"+empresa_id,{}).then(this.handleResponse)
            Rest.get("contato-empresa/all", {empresa_id:empresa_id}).then(this.handleReceiveDataPage);
    
        }
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

	render()
	{
       let input_fields = [{label: 'page.contato_empresa.fields.nome', field: "nome"}, {label: 'page.contato_empresa.fields.cargo', field: "cargo"}];
       let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}/>;
		return (
			<TableData filter={filter}  onClickPage={ this.handleClickPage } title='page.contato_empresa.list.title'
				fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
				actions={ this.state.actions } addUrl='/contato-empresa/add' onEdit={ this.handleOnEditAction }
				onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}


}

/*----------------------------------------------------------------------------------------------------*/

class ContatoEmpresaAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'contato-empresa/add',
		title: Messages.getMessage('menu.contato_empresa.title')
	};
    


	render()
	{
        let principal = [{value:true, nome:"Sim"}, {value:false, nome:"No"}];

		return (
			<FormPage title="page.contato_empresa.add.title">
				<FormRow>
					<InputInGroup type="text"  name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.nome' required="required" colsize="6" maxLength="100" />

					<InputInGroup type="text" name="cargo" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.cargo' required="required" colsize="6" maxLength="50"/>
				</FormRow>
				<FormRow>
					<InputInGroup type="email" name="email" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.email' required="required" colsize="6" maxlength="50" />
				</FormRow>
				<FormRow>
					<InputInGroup type="tel" class="phone" name="telefone" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.telefone' required="required" colsize="6" maxlength="15" />
					<InputInGroup type="tel" class="cellphone" name="celular" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.celular' required="required" colsize="6" maxlength="15"/>
                    <SelectField empty={ true } value_name="principal" name="principal" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.principal' required="required" colsize="2" options={principal} />
				</FormRow>

				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}


/*----------------------------------------------------------------------------------------------------*/

class ContatoEmpresaEdit extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
    }
    static defaultProps = {
        urlBase: 'contato-empresa/edit',
        title: Messages.getMessage('menu.contato_empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "contato-empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ))
    }

    async handleOnSubmit(e) {
        Rest.edit(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
    }

	render()
	{
	        let principal = [{ value:true, nome:"Sim"}, { value:false, nome:"No"}];

		return (
			this.state.error ?
				( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
            <FormPage title="page.contato_empresa.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" value = {this.state.nome} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.nome' required="required" colsize="6" />

					<InputInGroup type="text" name="cargo" value = {this.state.cargo} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.cargo' required="required" colsize="6" />
				</FormRow>
				<FormRow>
					<InputInGroup type="email" name="email" value = {this.state.email} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.email' required="required" colsize="6" maxlength="50" />
				</FormRow>
				<FormRow>
					<InputInGroup type="tel" name="telefone" value = {this.state.telefone} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.telefone' required="required" colsize="6" maxlength="15" />
					<InputInGroup type="tel" name="celular" value = {this.state.celular} errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.celular' required="required" colsize="6" maxlength="15"/>
                    <SelectField empty={ true } value_name="principal" name="principal" value = {this.state.principal ? "Sim" : "No" } errors={ this.state.fieldErrors }  onChange={ this.handleChange }
						label='page.contato_empresa.fields.principal' required="required" colsize="2" options={principal} />
				</FormRow>


				<FormRow>
					<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class ContatoEmpresaView extends BasePageForm
{

    constructor(props) {
        super(props);

        this.handleResponse = this.handleResponse.bind(this);
        this.onClickEdit = this.onClickEdit.bind(this);
    }
    onClickEdit(event) {
        console.log(event.target);
        let url = "edit";
        let id = this.state.id;
        this.props.history.push({
            pathname: url,
            state: {item_id: id}
        });
    }
    static defaultProps = {
        urlBase: 'contato-empresa/view',
        title: Messages.getMessage('menu.contato_empresa.title')
    };

    componentDidMount() {
        if (this.props.location.state === undefined){
            this.setState(({error:true}));
            return;
        }
        let id = this.props.location.state.item_id;
        Rest.view( "contato-empresa/view/" + id, this.state).then(this.handleResponse);
    }

    handleResponse(data) {
        this.setState((
            data.data
        ));
    }
    render()
    {
        
        let fields = [];
            
        if(this.state.principal === true){
            fields = [
                {label:"ID: ", value:this.state.id},
                {label:"Empresa: ", value:this.state.empresa_nome},
                {label:"Nome: ", value:this.state.nome},
                {label:"Cargo: ", value:this.state.cargo},
                {label:"Email: ", value:this.state.email},
                {label:"Telefone: ", value:this.state.telefone},
                {label:"Celular: ", value:this.state.celular},
                {label:"Este  o contato principal da empresa."}];
        }else{
            fields = [
                {label:"ID: ", value:this.state.id},
                {label:"Empresa: ", value:this.state.empresa_nome},
                {label:"Nome: ", value:this.state.nome},
                {label:"Cargo: ", value:this.state.cargo},
                {label:"Email: ", value:this.state.email},
                {label:"Telefone: ", value:this.state.telefone},
                {label:"Celular: ", value:this.state.celular},
                {label:"Este no  o contato principal da empresa."}];
        }

        
        return (
            this.state.error ?
            ( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
                (<BasicView title={"Contato " + this.state.nome} url={"#contato_empresa/edit?id=" + this.state.id}
                            onClickEdit={this.onClickEdit} fields={fields}/>)
        );
    }
}

export {ContatoEmpresaList, ContatoEmpresaView, ContatoEmpresaAdd, ContatoEmpresaEdit};